import { Component, Fragment, ReactNode } from 'react'

import Label from 'semantic-ui-react/dist/commonjs/elements/Label'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'
import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon/Icon'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'

import EditorPreview from '../global/EditorPreview'
import { AuthorIconLogDefaultWrapper } from '../global/AuthorIconLog'
import HojaTrabajoContext from './HojaTrabajoContext'
import { HojaTrabajoUpdateUtils } from './utils'

import DualListBox from 'react-dual-listbox'
import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider/Divider'
import Accordion from 'semantic-ui-react/dist/commonjs/modules/Accordion/Accordion'
import Form from 'semantic-ui-react/dist/commonjs/collections/Form/Form'

type AppVisitaDefaultTabProps = {
  visita: ProyectoPasoVisita
}

export class AppVisitaDefaultTab extends Component<AppVisitaDefaultTabProps> {
  getStatusLabel() {
    const { visita } = this.props

    let status_label = null

    if (visita.status) {
      status_label = (
        <Label
          style={{ backgroundColor: visita.status_color }}
          className={`visita-status visita-status-${_.str.underscored(visita.status)}`}
          size='mini'
        >
          {visita.status}
        </Label>
      )
    }

    return status_label
  }

  render() {
    const { visita } = this.props

    return (
      <Fragment>
        <span className='tabname' title={visita.label}>
          {visita.label}
        </span>
        <AuthorIconLogDefaultWrapper model={visita} />
        {this.getStatusLabel()}
      </Fragment>
    )
  }
}

type PasoObjetivoPaneProps = {
  isEditable: boolean
}

export class PasoObjetivoPane extends Component<PasoObjetivoPaneProps> {
  static contextType = HojaTrabajoContext

  static defaultProps: PasoObjetivoPaneProps = {
    isEditable: false
  }

  context: HojaTrabajoContextType

  onChangeValue = (editor: CKEDITOR.editor) => {
    const objetivo = editor.getData()
    const { hoja_trabajo, update } = this.context

    $.blockUI(create_blockUI_loading_message('Guardando Cambios'))
    const url = url_for(`proyecto/${hoja_trabajo.proyecto.id}/master_data/paso/${hoja_trabajo.paso.id}/objetivo`)
    $.ajax(url, { type: 'POST', data: { objetivo }, dataType: 'json' }).done(() => {
      let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateField(hoja_trabajo, 'paso', { objetivo })
      update(hoja_trabajo_new)
      $.unblockUI()
    })
  }

  render() {
    let { hoja_trabajo }: { hoja_trabajo: HojaTrabajo } = this.context
    const ckeditorOptions = {
      height: 150,
      toolbar: [
        { name: 'basicstyles', groups: [ 'basicstyles', 'cleanup' ], items: [ 'Bold', 'Italic' ] },
        {
          name: 'paragraph',
          groups: [ 'list', 'indent', 'blocks', 'align', 'bidi' ],
          items: [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', '-', 'TextColor' ]
        }
      ],
      autoParagraph: false,
      removePlugins: 'elementspath, uploadfile, resize',
      extraPlugins: 'openlink'
    }

    return (
      <div className='hoja-trabajo-objetivo hoverable-actions'>
        <EditorPreview
          className='objetivo-text-editor'
          ckeditorOptions={ckeditorOptions}
          prefixPreview='Objetivo:'
          label=' '
          labelTitle='Objetivo: '
          data={hoja_trabajo.paso.objetivo}
          editable={this.props.isEditable && user_has_role_permission('paso_objetivo_edit')}
          onSave={this.onChangeValue}
        />
      </div>
    )
  }
}

type PasoVisitaStatusActionsProps = {
  visita: ProyectoPasoVisita
  revisadoDisabledText?: string
  completadoDisabledText?: string
}

export class PasoVisitaStatusActions extends Component<PasoVisitaStatusActionsProps> {
  static contextType = HojaTrabajoContext
  context: HojaTrabajoContextType

  onClickCompletado = () => {
    const { hoja_trabajo } = this.context
    const url = url_for(
      `proyecto/${hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${hoja_trabajo.paso.id}/visita/${this.props.visita
        .id_visita}/save`
    )

    let loadingMessage = 'Marcando como Completado...'
    $.blockUI(create_blockUI_loading_message(loadingMessage))
    $.ajax(url, { type: 'POST', data: { op: 'c' }, dataType: 'json' }).done((res) => {
      if (res.hasErrors) {
        $.unblockUI()
        bootbox.alert(res.message)
      } else {
        this.updateVisita(res.response.record)
        $.unblockUI()
      }
    })
  }

  onClickRevisado = () => {
    const { hoja_trabajo } = this.context
    const url = url_for(
      `proyecto/${hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${hoja_trabajo.paso.id}/visita/${this.props.visita
        .id_visita}/save`
    )

    let loadingMessage = 'Marcando como Revisado...'
    $.blockUI(create_blockUI_loading_message(loadingMessage))
    $.ajax(url, { type: 'POST', data: { op: 'r' }, dataType: 'json' }).done((res) => {
      if (res.hasErrors) {
        $.unblockUI()
        bootbox.alert(res.message)
      } else {
        this.updateVisita(res.response.record)
        $.unblockUI()
      }
    })
  }

  getRevisadoDisabledText() {
    if (_.include([ '1', '2', '3' ], this.props.visita.id_status) || this.props.completadoDisabledText) {
      return 'Esta visita debe estar primero en estado COMPLETADO, para poder marcar esta hoja de trabajo como REVISADA.'
    } else if (_.include([ '6' ], this.props.visita.id_status)) {
      return 'Esta visita  tiene Notas de Revisin que deben primero ser atendidas para poder marcar esta hoja de trabajo como REVISADA.'
    } else if (this.props.visita.additionals.revisiones > 0) {
      return 'Esta visita tiene Notas de Revisin pendientes por revisar que deben primero ser revisadas para poder marcar esta hoja de trabajo como REVISADA.'
    }

    return this.props.revisadoDisabledText
  }

  updateVisita(_visita: any) {
    const { hoja_trabajo, update } = this.context
    let hoja_trabajo_new = HojaTrabajoUpdateUtils.updateVisita(hoja_trabajo, this.props.visita, _visita)
    update(hoja_trabajo_new)
  }

  render() {
    const { visita } = this.props
    const revisadoDisabledText = this.getRevisadoDisabledText()

    let icon_revisado_alert
    if (
      visita.fecha_revision && // Tiene fecha de revision
      ((visita.fecha_completado && Date.create(visita.fecha_completado) > Date.create(visita.fecha_revision)) || // Fue completado despues de revisado
        !_.includes([ '5', '6' ], '' + visita.id_status)) // No esta en estado Revisado o Notas de Revision
    ) {
      icon_revisado_alert = (
        <Popup
          trigger={<Icon color='red' name='warning circle' />}
          content={'Esta visita ha recibido modificaciones despus de su ltima revisin.'}
        />
      )
    }

    return (
      <div className='actions ab-status-actions'>
        {user_has_role_permission('paso_completado') && (
          <Popup
            trigger={
              <div className='button-wrapper'>
                <Button
                  primary
                  icon
                  compact
                  onClick={this.onClickCompletado}
                  disabled={this.props.completadoDisabledText != null}
                >
                  {' '}
                  <Icon name='check' /> Completado
                </Button>
              </div>
            }
            content={this.props.completadoDisabledText}
            header='Marcar como Completado | Opcin deshabilitada'
            disabled={this.props.completadoDisabledText == null}
            className='disable-text'
            on='hover'
          />
        )}
        {user_has_role_permission([ 'paso_revisado' ]) && (
          <Popup
            trigger={
              <div className='button-wrapper'>
                <Button
                  color='olive'
                  icon
                  compact
                  onClick={this.onClickRevisado}
                  disabled={revisadoDisabledText != null}
                >
                  {' '}
                  <Icon name='check' fitted />
                  <Icon name='check' /> Revisado {icon_revisado_alert}
                </Button>
              </div>
            }
            content={revisadoDisabledText}
            header='Marcar como Revisado | Opcin deshabilitada'
            disabled={revisadoDisabledText == null}
            className='disable-text'
            on='hover'
            position='top right'
          />
        )}
      </div>
    )
  }
}

type PasoVisitaAssocProps = {
  hoja_trabajo: HojaTrabajo
  trigger: ReactNode
}
type PasoVisitaAssocState = {
  open: boolean
  newVisitaOpen: boolean
  selectedVisitas: number[]
}

export class PasoVisitaAssoc extends Component<PasoVisitaAssocProps, PasoVisitaAssocState> {
  constructor(props: PasoVisitaAssocProps) {
    super(props)

    this.state = {
      open: false,
      newVisitaOpen: false,
      selectedVisitas: _.filter(_.map(this.props.hoja_trabajo.visitas, (item, id) => (item.id ? id : null)))
    }
  }

  getModal() {
    if (!this.state.open) {
      return null
    }

    const icons = {
      moveLeft: <i className='icon angle left' />,
      moveAllLeft: <i className='icon angle double left' />,
      moveRight: <i className='icon angle right' />,
      moveAllRight: <i className='icon angle double right' />,
      moveDown: <i className='icon angle down' />,
      moveUp: <i className='icon angle up' />
    }

    const { hoja_trabajo } = this.props

    const visitas = hoja_trabajo.visitas
    let options: { value: number; label: string }[] = []

    _.forEach(visitas, function(visita, id) {
      options.push({
        value: id,
        label: visita.label
      })
    })

    const onChange = (selectedVisitas: number[]) => {
      this.setState({ selectedVisitas })
    }

    const onClose = () => {
      this.setState({ open: false })
    }

    const onToggle = () => {
      this.setState((state) => {
        return { newVisitaOpen: !state.newVisitaOpen }
      })
    }

    return (
      <Modal key='modal-assoc' open closeIcon dimmer='inverted' size='small' onClose={onClose} className='modal-ab'>
        <Modal.Header> Asociacin de Visitas a este Paso</Modal.Header>
        <Modal.Content>
          <form
            method='post'
            className='ui form'
            action={url_for(`proyecto/${hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${hoja_trabajo.paso.id}/visita`)}
          >
            <div style={{ marginBottom: '15px' }}>Asocie las visitas a relacionar con este paso: </div> <br />
            <DualListBox
              name='visitas'
              options={options}
              availableLabel='Por asociar'
              selectedLabel='Asociadas'
              selected={this.state.selectedVisitas}
              icons={icons}
              onChange={onChange}
            />
            <Divider horizontal>O</Divider>
            <Accordion>
              <Accordion.Title active={this.state.newVisitaOpen} onClick={onToggle}>
                <Icon name='caret right' /> Crear Nueva Visita
              </Accordion.Title>
              <Accordion.Content active={this.state.newVisitaOpen}>
                <Form.Field inline>
                  <label>Nombre de la visita</label>
                  <div className='ui input'>
                    <input placeholder='Nombre de la visita' name='visita_name' type='text' />
                  </div>
                </Form.Field>
              </Accordion.Content>
            </Accordion>
            <button className='primary small ui button right floated'>Guardar</button>
            <button className='negative small ui button right floated' type='button' onClick={onClose}>
              Cancelar
            </button>
            <div className='clearfix' />
          </form>
        </Modal.Content>
      </Modal>
    )
  }
  render() {
    return [
      <div key='trigger' onClick={() => this.setState({ open: true })}>
        {this.props.trigger}
      </div>,
      this.getModal()
    ]
  }
}
