import { Component } from 'react'

import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'

class TabPaneVisitaBase<P extends TabPaneVisitaProps, S extends TabPaneVisitaState> extends Component<P, S> {
  static defaultProps = {
    type: 'DEFAULT'
  }

  static getDerivedStateFromProps(nextProps: TabPaneVisitaProps, prevState: TabPaneVisitaState) {
    if (nextProps.visita.id_status != prevState.id_status) {
      return { id_status: '' + nextProps.visita.id_status }
    }

    return null
  }

  onClickCompletado = () => {
    const url = url_for(
      `proyecto/${this.props.visita.HojaTrabajo.proyecto.id}/hoja-trabajo/paso/${this.props.visita.HojaTrabajo.paso
        .id}/visita/${this.props.visita.id_visita}/save`
    )
    let loadingMessage =
      this.props.type == 'PERIODO' ? 'Marcando periodo como Completado...' : 'Marcando visita como Completada...'
    $.blockUI(create_blockUI_loading_message(loadingMessage))
    $.ajax(url, { type: 'POST', data: { op: 'c' }, dataType: 'json' }).done((res) => {
      if (res.hasErrors) {
        $.unblockUI()
        bootbox.alert(res.message)
      } else {
        this.updateVisita(res.response.record)
        $.unblockUI()
      }
    })
  }

  onClickRevisado = () => {
    const url = url_for(
      `proyecto/${this.props.visita.HojaTrabajo.proyecto.id}/hoja-trabajo/paso/${this.props.visita.HojaTrabajo.paso
        .id}/visita/${this.props.visita.id_visita}/save`
    )
    let loadingMessage =
      this.props.type == 'PERIODO' ? 'Marcando periodo como Revisado...' : 'Marcando visita como Revisada...'
    $.blockUI(create_blockUI_loading_message(loadingMessage))
    $.ajax(url, { type: 'POST', data: { op: 'r' }, dataType: 'json' }).done((res) => {
      if (res.hasErrors) {
        $.unblockUI()
        bootbox.alert(res.message)
      } else {
        this.updateVisita(res.response.record)
        $.unblockUI()
      }
    })
  }

  updateVisita(_visita: any) {
    let visita: ProyectoPasoVisita = Object.assign(this.props.visita, _visita)
    this.props.onChange(visita)
    this.setState({ id_status: '' + visita.id_status })
  }

  onUpdate = (visitas: ProyectoPasoVisita[], instrucciones?: InstruccionDataModifiedSchema[]) => {
    if (!$.isArray(visitas)) {
      visitas = [ visitas ]
    }
    this.updateVisita(visitas[0])

    // Actualizar el estado de las demas visitas afectadas
    visitas = _.rest(visitas)
    if (_.size(visitas)) {
      _.forEach(visitas, (visita: ProyectoPasoVisita) => {
        let _visita = _.find(this.props.visita.HojaTrabajo.visitas, { id_visita: visita.id_visita })
        _visita = Object.assign(_visita, visita)
      })
    }

    // Actualizar la informacion de las instrucciones afectadas

    if (instrucciones) {
      _.forEach(instrucciones, (instruccion_mdata: InstruccionDataModifiedSchema) => {
        let visita = _.find(this.props.visita.HojaTrabajo.visitas, { id_visita: instruccion_mdata.id_visita })

        switch (instruccion_mdata.cmd) {
          case 'CREATE':
            visita.instructions.push(instruccion_mdata.data)
            break
          case 'UPDATE':
            visita.instructions = _.map(visita.instructions, function(instruccion) {
              if (instruccion.local_id == instruccion_mdata.data.local_id) {
                return Object.assign(instruccion, instruccion_mdata.data)
              } else {
                return instruccion
              }
            })
            break
        }
      })
    }
  }

  getCompletadoDisabledText(): string | null {
    return null
  }

  getRevisadoDisabledText(): string | null {
    let modelLabel = this.props.type == 'PERIODO' ? 'Este periodo' : 'Esta visita'
    if (_.includes([ '1', '2', '3' ], this.state.id_status) || this.getCompletadoDisabledText()) {
      return (
        modelLabel + ' debe estar primero en estado COMPLETADO, para poder marcar esta hoja de trabajo como REVISADA.'
      )
    } else if (_.includes([ '6' ], this.state.id_status)) {
      return (
        modelLabel +
        ' tiene Notas de Revisin que deben primero ser atendidas para poder marcar esta hoja de trabajo como REVISADA.'
      )
    } else if (this.props.visita.additionals.revisiones > 0) {
      return (
        modelLabel +
        ' tiene Notas de Revisin pendientes por revisar que deben primero ser revisadas para poder marcar esta hoja de trabajo como REVISADA.'
      )
    }

    return null
  }

  renderStatusActions() {
    if (!this.props.isEditable) {
      return null
    }

    const revisado_disabled_text = this.getRevisadoDisabledText()

    let icon_revisado_alert
    const visita = this.props.visita
    if (
      visita.fecha_completado &&
      visita.fecha_revision &&
      Date.create(visita.fecha_completado) > Date.create(visita.fecha_revision)
    ) {
      let modelLabel = this.props.type == 'PERIODO' ? 'Este periodo' : 'Esta visita'
      icon_revisado_alert = (
        <Popup
          trigger={<Icon color='red' name='warning circle' />}
          content={modelLabel + ' ha recibido modificaciones despus de su ltima revisin.'}
        />
      )
    }

    const completado_disabled_text = this.getCompletadoDisabledText()

    return (
      <div className='actions ab-status-actions'>
        {user_has_role_permission('paso_completado') && (
          <Popup
            trigger={
              <div className='button-wrapper'>
                <Button
                  primary
                  icon
                  compact
                  onClick={this.onClickCompletado}
                  disabled={completado_disabled_text != null}
                >
                  {' '}
                  <Icon name='check' /> Completado
                </Button>
              </div>
            }
            content={completado_disabled_text}
            header='Marcar como Completado | Opcin deshabilitada'
            disabled={completado_disabled_text == null}
            className='disable-text'
            on='hover'
          />
        )}
        {user_has_role_permission([ 'paso_revisado' ]) && (
          <Popup
            trigger={
              <div className='button-wrapper'>
                <Button
                  color='olive'
                  icon
                  compact
                  onClick={this.onClickRevisado}
                  disabled={revisado_disabled_text != null}
                >
                  {' '}
                  <Icon name='check' fitted />
                  <Icon name='check' /> Revisado {icon_revisado_alert}
                </Button>
              </div>
            }
            content={revisado_disabled_text}
            header='Marcar como Revisado | Opcin deshabilitada'
            disabled={revisado_disabled_text == null}
            className='disable-text'
            on='hover'
          />
        )}
      </div>
    )
  }
}

export default TabPaneVisitaBase
