export const getNombreCuenta = (cuentas: ProyectoAjusteCuenta[], cuenta: string) => {
  let found = _.findWhere(cuentas, { cuenta })

  if (found) {
    return found.nombre_cuenta
  }

  const cleanCuenta = function(c: string) {
    return c.replace(/\D/g, '')
  }

  found = _.findWhere(
    _.map(cuentas, function(item) {
      return { cuenta: cleanCuenta(item.cuenta), cuenta_real: item.cuenta_real, nombre_cuenta: item.nombre_cuenta }
    }),
    { cuenta: cleanCuenta(cuenta) }
  )

  return found ? found.nombre_cuenta : ''
}
