import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab/Tab'
import Progress from 'semantic-ui-react/dist/commonjs/modules/Progress/Progress'
import Table from 'semantic-ui-react/dist/commonjs/collections/Table/Table'

import TableRowInstruccion from './TableRowInstruccion'
import { PasoObjetivoPane } from './AppVisitaTab'
import TabPaneVisitaBase from './TabPaneVisitaBase'
import MenuSidebarVisita from './MenuSidebarVisita'

import * as uuid from 'uuid'
import { DefaultErrorHandler } from '../global/ErrorHandler'

export default class TabPaneVisita extends TabPaneVisitaBase<TabPaneVisitaProps, TabPaneVisitaState> {
  constructor(props: TabPaneVisitaProps) {
    super(props)

    this.state = {
      progress: this.calculateProgress(),
      id_status: '' + props.visita.id_status,

      openAsignarUsuarioModal: false
    }
  }

  componentDidMount() {
    const $table = $(`#table-visita-instrucciones-${this.props.visita.id}`)

    $table.floatThead()

    if (this.props.isEditable && this.isAllowedAddInstructions() && user_has_role_permission('instruccion_edit')) {
      $table.tableDnD({
        dragHandle: '.sort-handler',
        onDragClass: 'dragging',
        onDragStop: (table: any) => {
          let serialize = []
          const rows = table.rows

          for (let i: number = 1; i < rows.length; i++) {
            let rowId = rows[i].id.substr(18)
            serialize.push(rowId)
            $(rows[i]).find('.instruction-iteration').text(_.padLeft('' + i, 3, '0') + '.')
          }

          const url = url_for(`proyecto/${this.props.visita.HojaTrabajo.proyecto.id}/master_data/instruccion/sort`)
          $.ajax(url, {
            type: 'POST',
            data: {
              sort: serialize,
              id_paso: this.props.visita.HojaTrabajo.paso.id,
              id_visita: this.props.visita.id_visita
            },
            dataType: 'json'
          })
        }
      })
    }
  }

  componentWillUnmount() {
    $(`#table-visita-instrucciones-${this.props.visita.id}`).floatThead('destroy')
  }

  componentDidUpdate() {
    const $table = $(`#table-visita-instrucciones-${this.props.visita.id}`)

    setTimeout(function() {
      $table.floatThead('reflow')
    }, 1000)
  }

  onClickAddInstruccion = () => {
    this.props.visita.instructions.push({
      local_id: uuid.v1(),
      category: null,
      description: null,
      Attachments: [],
      is_done: false,
      Visita: this.props.visita,
      updated_at: null
    })
    this.props.visita.updated_at = Date.create().format('{year}-{MM}-{dd} {hh}:{mm}:{ss}')

    this.recalculateProgress()

    if (this.props.isEditable && this.isAllowedSortInstructions() && user_has_role_permission('instruccion_edit')) {
      setTimeout(() => {
        $(`#table-visita-instrucciones-${this.props.visita.id}`).tableDnDUpdate()
      }, 300)
    }
  }

  calculateProgress() {
    const done_count = this.props.visita.instructions.filter((_item) => _item.is_done).length
    return this.props.visita.instructions.length == 0
      ? 0
      : Math.round(100 * done_count / this.props.visita.instructions.length)
  }

  isAllowedAddInstructions() {
    return !_.includes([ '2A4', '2A3' ], this.props.visita.HojaTrabajo.paso.code)
  }

  isAllowedSortInstructions() {
    return !_.includes([ '2A4', '2A3' ], this.props.visita.HojaTrabajo.paso.code)
  }

  recalculateProgress() {
    const progress = this.calculateProgress()

    this.setState({ progress })
  }

  onUpdateInstruccion = (instruccion: Instruccion, key: number) => {
    this.props.visita.instructions[key] = instruccion
    this.recalculateProgress()
  }

  onRemoveInstruccion = (key: number) => {
    this.props.visita.instructions = this.props.visita.instructions.filter((_, i) => i !== key)
    this.recalculateProgress()

    $(`#table-visita-instrucciones-${this.props.visita.id} tbody tr`).each(function(i) {
      $(this).find('.instruction-iteration').text(_.padLeft('' + (i + 1), 3, '0') + '.')
    })
  }

  getCompletadoDisabledText(): string | null {
    if (this.state.progress < 100) {
      return 'Para completar esta hoja de trabajo, es necesario que TODAS las instrucciones estn atendidas.'
    }

    return null
  }

  render() {
    const { active, visita } = this.props

    const menuSidebarActionItems = {
      _ajustes: this.props.type != 'PERIODO',
      _history: this.props.type != 'PERIODO',
      instructions_add:
        this.props.isEditable && this.isAllowedAddInstructions() && user_has_role_permission('instruccion_create')
          ? {
              link: true,
              icon: { name: 'plus', size: 'large' },
              title: 'Aadir Instruccin',
              onClick: this.onClickAddInstruccion
            }
          : null
    }

    return (
      <Tab.Pane active={active}>
        <div className='visita-progress-container'>
          <Progress className='visita-progress' size='tiny' color='green' percent={this.state.progress} />
          <div className='visita-progress-label'>{`${this.state.progress}%`}</div>
        </div>

        <DefaultErrorHandler>
          <MenuSidebarVisita visita={visita} actionItems={menuSidebarActionItems} />
        </DefaultErrorHandler>

        <PasoObjetivoPane isEditable={this.props.isEditable} />

        <Table stackable className='table-visita-instrucciones' id={`table-visita-instrucciones-${visita.id}`}>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell className='th-instruccion' colSpan='2'>
                Instrucciones
              </Table.HeaderCell>
              <Table.HeaderCell className='th-accion'>Accin</Table.HeaderCell>
              <Table.HeaderCell className='th-observaciones'>Observaciones/Evidencia</Table.HeaderCell>
              <Table.HeaderCell className='th-adjuntos'>Adjuntos</Table.HeaderCell>
              <Table.HeaderCell className='th-actions computer'>&nbsp;</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>{this.renderInstrucciones(visita)}</Table.Body>
        </Table>

        <div>
          {_.size(visita.instructions) > 0 &&
          this.props.isEditable &&
          this.isAllowedAddInstructions() &&
          user_has_role_permission('instruccion_create') && (
            <div className='ab-actions-group compact-actions'>
              <Button
                primary
                floated='left'
                compact
                icon='plus'
                content='Aadir Instruccin'
                onClick={this.onClickAddInstruccion}
              />
            </div>
          )}

          {_.size(visita.instructions) > 0 && this.renderStatusActions()}
        </div>
      </Tab.Pane>
    )
  }

  renderInstrucciones(visita: ProyectoPasoVisita) {
    if (_.size(visita.instructions) == 0) {
      return (
        <Table.Row>
          <Table.Cell colSpan='6' style={{ textAlign: 'center' }}>
            <div className='alert alert-block nocontent'>
              <h3>Sin instrucciones </h3>
              <p>
                <span className='icon-warning-sign' />Esta hoja de trabajo an no tiene instrucciones registradas.{' '}
              </p>
              {this.props.isEditable &&
              user_has_role_permission('instruccion_create') && [
                <Button
                  key='button'
                  content='Agregar instruccin'
                  color='green'
                  compact
                  icon='info circle'
                  onClick={this.onClickAddInstruccion}
                  style={{ marginTop: '30px' }}
                />
              ]}
            </div>
          </Table.Cell>
        </Table.Row>
      )
    }

    let instructions = _.map(visita.instructions, function(item, k) {
      item.index = k
      return item
    })
    let instructionGroups = _.values(
      _.map(_.groupBy(instructions, 'category'), function(item, k) {
        return { text: k, instructions: item }
      })
    )

    let rows: JSX.Element[] = []
    instructionGroups.forEach((instruccionGroup: { text: string; instructions: Instruccion[] }, i: number) => {
      if (instruccionGroup.text != '' && instruccionGroup.text != 'null' && instruccionGroup.text != null) {
        rows.push(
          <tr key={i} className='macroinstruccion-row nodrag nodrop'>
            <td colSpan={6}>{instruccionGroup.text}</td>
          </tr>
        )
      }

      instruccionGroup.instructions.forEach((instruccion: Instruccion, j: number) => {
        instruccion.Visita = visita
        rows.push(
          <TableRowInstruccion
            key={instruccion.local_id}
            index={instruccion.index}
            iteration={j}
            instruccion={instruccion}
            isEditable={this.props.isEditable}
            isSortable={this.props.isEditable && this.isAllowedSortInstructions()}
            onUpdate={this.onUpdateInstruccion}
            onRemove={this.onRemoveInstruccion}
            onUpdateVisita={this.onUpdate}
          />
        )
      })
    })

    return rows
  }
}
