export class HallazgoHojaTrabajoModelManager implements MenuElementModelManager<ProyectoHallazgo> {
  private static instance: HallazgoHojaTrabajoModelManager

  static getInstance(): HallazgoHojaTrabajoModelManager {
    if (!HallazgoHojaTrabajoModelManager.instance) {
      HallazgoHojaTrabajoModelManager.instance = new this()
    }

    return HallazgoHojaTrabajoModelManager.instance
  }

  /**
   * Descarga el listado de los hallazgos.
   *
   * @param props
   */
  getJson(props: DefaultMenuElementProps<ProyectoHallazgo>): Promise<ProyectoHallazgo[]> {
    const { id_paso, id_visita } = props.moduleParams

    return new Promise((resolve) => {
      $.getJSON(url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/hallazgo/json`)).done((res) => {
        resolve(_.isEmpty(res.response) ? [] : res.response)
      })
    })
  }

  createItem(props: DefaultMenuElementProps<ProyectoHallazgo>, data: any): Promise<ProyectoHallazgo> {
    const { id_paso, id_visita } = props.moduleParams
    const url = url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/hallazgo`)

    return new Promise<ProyectoHallazgo>((resolve) => {
      $.post(url, { data }).done((res) => {
        resolve(res.response.record)
      })
    })
  }

  saveItem(props: DefaultMenuElementProps<ProyectoHallazgo>, item: any, data: any): Promise<ProyectoHallazgo> {
    let url: string
    if (_.isEqual(data, { status: 'COMPLETADO' })) {
      url = `proyecto/:id_proyecto/paso/:id_paso/visita/:id_visita/hallazgo/:id/c`
    } else if (_.isEqual(data, { status: 'REVISADO' })) {
      url = `proyecto/:id_proyecto/paso/:id_paso/visita/:id_visita/hallazgo/:id/r`
    } else {
      url = `proyecto/:id_proyecto/paso/:id_paso/visita/:id_visita/hallazgo/:id`
    }

    const { id_paso, id_visita } = props.moduleParams

    return new Promise<ProyectoHallazgo>((resolve) => {
      $.post(url_for2(url, { id_paso, id_visita, id: item.id }), { data }).done((res) => {
        resolve(res.response.record)
      })
    })
  }

  deleteItem(props: DefaultMenuElementProps<ProyectoHallazgo>, item: any): Promise<null> {
    const { id_paso, id_visita } = props.moduleParams
    const url = `proyecto/:id_proyecto/paso/:id_paso/visita/:id_visita/hallazgo/:id`

    return new Promise<null>((resolve) => {
      $.ajax({ url: url_for2(url, { id_paso, id_visita, id: item.id }), type: 'DELETE' }).done((res) => {
        if (res.status == 1) {
          resolve()
        }
      })
    })
  }

  parseItems(items: any): any {
    return items
  }
}

export class RevisionHojaTrabajoModelManager implements MenuElementModelManager<ProyectoRevisionTree> {
  private static instance: RevisionHojaTrabajoModelManager

  private hojaTrabajoContext: HojaTrabajoContextType

  static getInstance(hojaTrabajoContext: HojaTrabajoContextType): RevisionHojaTrabajoModelManager {
    if (!RevisionHojaTrabajoModelManager.instance) {
      RevisionHojaTrabajoModelManager.instance = new this()
      RevisionHojaTrabajoModelManager.instance.hojaTrabajoContext = hojaTrabajoContext
    }

    return RevisionHojaTrabajoModelManager.instance
  }

  /**
   * Descarga el listado de los notas de revision.
   *
   * @param props
   */
  getJson(props: DefaultMenuElementProps<ProyectoRevisionTree>): Promise<ProyectoRevisionTree[]> {
    const { id_paso, id_visita } = props.moduleParams

    return new Promise((resolve) => {
      $.getJSON(url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/revision`)).done((res) => {
        resolve(_.isEmpty(res.response.revisiones) ? [] : this.parseItems(res.response.revisiones))
      })
    })
  }

  createItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, data: any): Promise<ProyectoRevisionTree> {
    const { id_paso, id_visita } = props.moduleParams
    const url = url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/revision`)

    return new Promise<ProyectoRevisionTree>((resolve) => {
      $.post(url, data).done((res) => {
        resolve(res.response.revision)

        this.updateVisita(id_visita, res.response.paso_visita)
      })
    })
  }

  saveItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, item: any, data: any): Promise<ProyectoRevisionTree> {
    const { id_visita, id_paso } = props.moduleParams
    const url = url_for2(`proyecto/:id_proyecto/paso/${id_paso}/visita/${id_visita}/revision/${item.id}`)

    return new Promise<ProyectoRevisionTree>((resolve) => {
      $.post(url, data).done((res) => {
        resolve(res.response.revision)

        this.updateVisita(id_visita, res.response.paso_visita)
      })
    })
  }

  deleteItem(props: DefaultMenuElementProps<ProyectoRevisionTree>, item: any): Promise<null> {
    const { id_paso, id_visita } = props.moduleParams
    const url = `proyecto/:id_proyecto/paso/:id_paso/visita/:id_visita/revision/:item_id`

    return new Promise<null>((resolve) => {
      $.ajax({ url: url_for2(url, { id_paso, id_visita, item_id: item.id }), type: 'DELETE' }).done((res) => {
        resolve()

        this.updateVisita(id_visita, res.response.paso_visita)
      })
    })
  }

  parseItems(items: any): any {
    return items
  }

  updateVisita(id_visita: Readonly<string>, changes: any) {
    const { hoja_trabajo, update } = this.hojaTrabajoContext

    let hoja_trabajo_copy = Object.assign({}, hoja_trabajo)

    hoja_trabajo_copy.visitas = _.mapValues(hoja_trabajo_copy.visitas, (item: ProyectoPasoVisita) => {
      if (item.id_visita == id_visita) {
        item = Object.assign({}, item, changes)
      }
      return item
    })

    update(hoja_trabajo_copy)
  }
}
