import { PureComponent, SyntheticEvent, Fragment } from 'react'
import { MenuElementListPanel, MenuElementListActions } from './menuitem'
import { AuthorIconLogDefaultWrapper } from '../../global/AuthorIconLog'
import { StatusDivBlock } from '../../global/StatusButtonAction'
import Textarea from 'react-textarea-autosize'
import cx from 'classnames'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'

export default class RevisionList extends PureComponent<DefaultMenuItemListProps<ProyectoRevisionTree>> {
  render() {
    if (this.props.items === null) {
      return (
        <div style={{ height: 300, position: 'relative' }}>
          <div className='ui active inverted text loader elastic olive'>Cargando Notas de Revisin</div>
        </div>
      )
    } else if (_.size(this.props.items) == 0) {
      return <div className='empty nocontent'>0 Notas de Revisin</div>
    } else {
      const { items } = this.props

      const porRevisarItems = _.reject(items, { status: 'REVISADO' })
      const revisadoItems = _.filter(items, { status: 'REVISADO' })

      return (
        <MenuElementListPanel className='revision-list'>
          {_.size(porRevisarItems) == 0 ? (
            <div className='empty nocontent'>0 Notas de Revisin Abiertas</div>
          ) : (
            _.map(porRevisarItems, (item: ProyectoRevisionTree) => {
              return (
                <div key={item.id} className='listitem revision-listitem'>
                  <RevisionListItem
                    item={item}
                    isEditable={this.props.isEditable}
                    onSave={this.props.onSaveItem}
                    onClickDelete={this.props.onClickDeleteItem}
                  />
                </div>
              )
            })
          )}

          {_.size(revisadoItems) > 0 && <RevisionDisabledList items={revisadoItems} />}
        </MenuElementListPanel>
      )
    }
  }
}

class RevisionDisabledList extends PureComponent<{ items: ProyectoRevisionTree[] }, { collapsed: boolean }> {
  state = {
    collapsed: true
  }

  toggle = () => {
    this.setState((prevState) => {
      return { collapsed: !prevState.collapsed }
    })
  }

  render() {
    const { items } = this.props

    return (
      <div style={{ marginTop: 20, marginBottom: 20, padding: '0 10px' }}>
        <h4 className='row clickable' onClick={this.toggle}>
          <i className={cx('icon caret', !this.state.collapsed ? 'down' : 'right')} /> Notas Revisadas
          <span className='toggle-action'>[{this.state.collapsed ? 'Mostrar' : 'Ocultar'}]</span>
          <span className='ui mini circular green label pull-right'>{_.size(items)}</span>
        </h4>
        {!this.state.collapsed && (
          <div className='revision-list-closed'>
            {_.map(items, (item: ProyectoRevisionTree) => {
              return (
                <div key={item.id} className='listitem revision-listitem'>
                  <RevisionListItem item={item} isEditable={false} />
                </div>
              )
            })}
          </div>
        )}
      </div>
    )
  }
}

type RevisionListItemState = {
  isEditMode: boolean
  isThreadOpen: boolean
}

class RevisionListItem extends PureComponent<
  DefaultMenuItemListItemProps<ProyectoRevisionTree>,
  RevisionListItemState
> {
  state = {
    isEditMode: false,
    isThreadOpen: false
  }

  onClickEdit = () => {
    this.setState({ isEditMode: true })
  }

  onClickDelete = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onClickDelete(this.props.item)
  }

  onClickNewComment = () => {
    if (this.props.isEditable) {
      this.setState({ isThreadOpen: true })
    }
  }

  onCancelNewComment = () => {
    this.setState({ isThreadOpen: false })
  }

  onChange = (changes: any) => {
    this.props.onSave(this.props.item, changes, 'Guardando cambios...').then(() => {
      this.setState({ isEditMode: false })
    })
  }

  onClickChangeStatus = (status: any) => {
    this.props.onSave(this.props.item, { op: status }, 'Marcando como ' + status + '...')
  }

  onAddComment = (params: { text: string; status: string }) => {
    const changes: any = { comment: params.text }
    if (params.status) {
      changes.op = params.status
    }

    this.props.onSave(this.props.item, changes, 'Guardando comentario...').then(() => {
      this.setState({ isThreadOpen: false })
    })
  }

  render() {
    const { item } = this.props
    return (
      <div>
        {this.props.isEditable && (
          <MenuElementListActions
            actions={{
              delete: { label: 'Eliminar', callback: this.onClickDelete, permission: 'revision_delete' }
            }}
          />
        )}
        <div className='row header-item'>
          <div className='pull-left'>
            <span className='title-h1'>Nota #{item.name}</span>
            <AuthorIconLogDefaultWrapper model={item} size='small' />
          </div>
          <StatusDivBlock
            wrapperClassName='pull-right text-mini'
            status={item.status || 'EN PROCESO'}
            className='mini'
            hideLabel
          />
        </div>

        <div className='author-info'>
          <i aria-hidden='true' className='user circle icon olive' style={{ float: 'left' }} />
          <div className='content'>
            <div className='header'>
              {item.Creator.User.name}
              <div className='pull-right description'>
                {Date.create(item.Creator.ts).format('{d} {Mon} {year} {h}:{mm} {tt}')}
              </div>
            </div>
          </div>
        </div>

        {this.props.isEditable && this.state.isEditMode ? (
          <RevisionEditForm item={item} onSubmit={this.onChange} />
        ) : (
          <Fragment>
            <p
              onClick={this.onClickNewComment}
              style={{ lineHeight: '1em' }}
              dangerouslySetInnerHTML={{ __html: item.description.replace(/\n/g, '<br/>') }}
            />
            <div className={cx('threadlist ui threaded comments', { empty: _.size(item.Comments) == 0 })}>
              {this.state.isThreadOpen && (
                <h5 className={cx('ui header', { dividing: _.size(item.Comments) == 0 })}>Comentarios</h5>
              )}
              {_.map(item.Comments, (comment) => {
                return (
                  <div className='comment' key={comment.id} onClick={this.onClickNewComment}>
                    <div className='avatar'>
                      <i
                        className={cx('icon user circle', {
                          green: comment.status == 'COMPLETADO',
                          olive: comment.status == 'REVISADO'
                        })}
                      />
                    </div>
                    <div className='content'>
                      <div className='author'>{comment.Creator.User.name}</div>
                      <div className='metadata'>
                        {Date.create(comment.Creator.ts).format('{d} {Mon} {year}  {h}:{mm} {tt}')}
                      </div>
                      <div className='text'>
                        <p dangerouslySetInnerHTML={{ __html: comment.comentario }} />
                      </div>
                    </div>
                  </div>
                )
              })}
            </div>
            {this.state.isThreadOpen && this.props.isEditable ? (
              <RevisionComentarioForm parentItem={item} onSave={this.onAddComment} onCancel={this.onCancelNewComment} />
            ) : (
              this.props.isEditable && (
                <div className='actions-wrapper'>
                  <div className='actions'>
                    <div className='group-actions'>
                      <button className='ui button icon blue mini compact' onClick={this.onClickNewComment}>
                        Responder
                      </button>
                    </div>
                    <div className='group-actions right-actions'>
                      <button
                        className='ui button icon primary mini compact'
                        onClick={() => this.onClickChangeStatus('COMPLETADO')}
                      >
                        <i className='icon check' /> Completar
                      </button>

                      <Popup
                        disabled={item.fecha_completado != null}
                        className='disable-text'
                        on='hover'
                        basic
                        content='Debe estar primero en estado COMPLETADO, para poder marcarla como REVISADA.'
                        trigger={
                          <div className='button-wrapper'>
                            <button
                              className='ui button mini compact olive'
                              disabled={item.fecha_completado == null}
                              onClick={() =>
                                item.fecha_completado == null ? void 0 : this.onClickChangeStatus('REVISADO')}
                            >
                              <i className='icon check fitted' />
                              <i className='icon check' /> Revisado
                            </button>
                          </div>
                        }
                      />
                    </div>
                  </div>
                </div>
              )
            )}
          </Fragment>
        )}
      </div>
    )
  }
}

type RevisionEditFormProps = {
  item: ProyectoRevisionTree
  onSubmit: (changes: { revision_text: string; op?: 'COMPLETADO' | 'REVISADO' }) => void
}

type RevisionEditFormState = {
  value: string
}

class RevisionEditForm extends PureComponent<RevisionEditFormProps, RevisionEditFormState> {
  constructor(props: RevisionEditFormProps) {
    super(props)
    this.state = {
      value: this.props.item.description
    }
  }

  onChange = (ev: SyntheticEvent) => {
    this.setState({ value: (ev.target as any).value })
  }

  onSubmit = (status?: 'COMPLETADO' | 'REVISADO') => {
    if (_.str.trim(this.state.value) == '') {
      bootbox.alertError('La Nota de Revisin no puede estar vaca.')
      return
    }

    const changes: any = { revision_text: _.str.trim(this.state.value) }
    if (status) {
      changes.op = status
    }
    this.props.onSubmit(changes)
  }

  render() {
    return (
      <Fragment>
        <Textarea
          minRows={2}
          maxRows={8}
          onChange={this.onChange}
          value={this.state.value}
          style={{ width: '98%' }}
          placeholder='Escriba aqu su Nota de Revisin'
        />

        <div style={{ marginTop: 8, textAlign: 'right' }}>
          <button className='ui button icon primary mini compact' onClick={() => this.onSubmit()}>
            Guardar
          </button>

          <button className='ui button icon primary mini compact' onClick={() => this.onSubmit('COMPLETADO')}>
            <i className='icon check' /> Completar
          </button>

          <Popup
            disabled={this.props.item.fecha_completado != null}
            className='disable-text'
            on='hover'
            basic
            content='Debe estar primero en estado COMPLETADO, para poder marcarla como REVISADA.'
            trigger={
              <div className='button-wrapper'>
                <button
                  className='ui button mini compact olive'
                  disabled={this.props.item.fecha_completado == null}
                  onClick={() => (this.props.item.fecha_completado == null ? void 0 : this.onSubmit('REVISADO'))}
                >
                  <i className='icon check fitted' />
                  <i className='icon check' /> Revisado
                </button>
              </div>
            }
          />
        </div>
      </Fragment>
    )
  }
}

type RevisionComentarioFormProps = {
  parentItem: ProyectoRevisionTree
  onSave: (params: { text: string; status: string }) => void
  onCancel: () => void
}
class RevisionComentarioForm extends PureComponent<RevisionComentarioFormProps> {
  cancelTimeout: number = null

  state = {
    value: ''
  }

  onChange = (ev: SyntheticEvent) => {
    this.setState({ value: (ev.target as any).value })
  }

  onTextareaKeyDown = (ev: React.KeyboardEvent) => {
    if (ev.keyCode == 27) {
      // on press Esc
      this.props.onCancel()
      this.cancelTimeout = null
    }

    if ((ev.ctrlKey || ev.metaKey) && (ev.keyCode == 13 || ev.keyCode == 10)) {
      // on press ctrl + Enter
      this.onSubmit()
    }
  }

  onSubmit = (status?: 'COMPLETADO' | 'REVISADO') => {
    if (_.str.trim(this.state.value) == '' && !status) {
      return
    }

    if (this.cancelTimeout != null) {
      window.clearTimeout(this.cancelTimeout)
      this.cancelTimeout = null
    }

    this.props.onSave({ text: _.str.trim(this.state.value), status })
  }

  onBlur = () => {
    this.cancelTimeout = window.setTimeout(() => {
      this.props.onCancel()
    }, 1000 * 0.5)
  }

  render() {
    return (
      <div style={{ marginBottom: 30 }}>
        <Textarea
          minRows={2}
          maxRows={5}
          autoFocus
          value={this.state.value}
          onChange={this.onChange}
          onKeyDown={this.onTextareaKeyDown}
          placeholder='Escriba aqu su comentario'
          style={{ width: '95%' }}
          onBlur={() => this.onBlur()}
        />

        <div style={{ textAlign: 'right', marginTop: 5 }}>
          <button className='ui button icon blue mini compact pull-left' onClick={() => this.onSubmit()}>
            Enviar
          </button>

          <button className='ui button icon primary mini compact' onClick={() => this.onSubmit('COMPLETADO')}>
            <i className='icon check' /> Completar
          </button>

          <Popup
            disabled={this.props.parentItem.fecha_completado != null}
            className='disable-text'
            on='hover'
            basic
            content='Debe estar primero en estado COMPLETADO, para poder marcarla como REVISADA.'
            trigger={
              <div className='button-wrapper'>
                <button
                  className='ui button mini compact olive'
                  disabled={this.props.parentItem.fecha_completado == null}
                  onClick={() => (this.props.parentItem.fecha_completado == null ? void 0 : this.onSubmit('REVISADO'))}
                >
                  <i className='icon check fitted' />
                  <i className='icon check' /> Revisado
                </button>
              </div>
            }
          />
        </div>
      </div>
    )
  }
}
