import { PureComponent, Fragment, SyntheticEvent, createRef } from 'react'

import Transition from 'semantic-ui-react/dist/commonjs/modules/Transition/Transition'
import cx from 'classnames'
import { SidebarPanelHeader } from './panel_header'
import { DefaultErrorHandler } from '../../global/ErrorHandler'

type BaseMenuItemProps = {
  open: boolean
  image: string
  counter: number
  onClick: () => void
  title: string
}

export class BaseMenuItem extends PureComponent<BaseMenuItemProps> {
  render() {
    const image_path = public_path('images/sidebar_right/')

    return (
      <a
        className={cx('item badged', !this.props.open || 'active')}
        title={this.props.title}
        onClick={this.props.onClick}
      >
        <img src={`${image_path}${this.props.image}.svg`} className='ui large image' /> {' '}
        <div className={cx('ui mini circular floating label right', this.props.counter == 0 ? 'green' : 'red')}>
          {this.props.counter}
        </div>
      </a>
    )
  }
}

type BaseMenuElementProps = {
  name: string
  counter: number
  title: string
  icon: string
  open: boolean
  onToggle: (item: string) => void
  onMountPanel?: () => void
}

export class BaseMenuElement extends PureComponent<BaseMenuElementProps> {
  onToggle = () => {
    this.props.onToggle(this.props.open ? null : this.props.name)
  }

  onClose = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onToggle(null)
  }

  render() {
    return (
      <Fragment>
        <DefaultErrorHandler>
          <BaseMenuItem
            counter={this.props.counter}
            open={this.props.open}
            title={this.props.title}
            image={this.props.icon}
            onClick={this.onToggle}
          />
          {this.props.open && (
            <Transition.Group animation='slide right' duration={1500}>
              <div
                className={`sidebar-fixed-content ui vertical right fixed menu menu-${this.props.name}`}
                style={{ right: 45 }}
              >
                <SidebarPanelHeader title={this.props.title} onClose={this.onClose} onMount={this.props.onMountPanel} />
                {this.props.children}
              </div>
            </Transition.Group>
          )}
        </DefaultErrorHandler>
      </Fragment>
    )
  }
}

export class MenuElementListPanel extends PureComponent<{ className?: string }> {
  listRef = createRef<HTMLDivElement>()
  listHeight: number = null

  componentDidMount() {
    if (this.listRef.current) {
      this.calculateHeight()
    }
  }

  componentDidUpdate() {
    if (this.listRef.current && !this.listHeight) {
      this.calculateHeight()
    }
  }

  calculateHeight() {
    const $list = $(this.listRef.current)
    this.listHeight = window.innerHeight - $list.position().top - 30
    $list.height(this.listHeight)
  }

  render() {
    return (
      <div className={`main-list ${this.props.className}`} ref={this.listRef}>
        {this.props.children}
      </div>
    )
  }
}

type MenuElementListActionsProps = {
  actions: {
    [s: string]: {
      label: string
      permission: string
      callback: (ev: SyntheticEvent) => void
    }
  }
}
export const MenuElementListActions = (props: MenuElementListActionsProps) => {
  return (
    <div className='pull-right sf_admin_td_actions'>
      <div className='btn-group dropdown-modern'>
        <a className='btn btn-mini dropdown-toggle' data-toggle='dropdown' href='#'>
          <span className='icon-tc-ellipsis-v' />
        </a>

        <ul className='dropdown-menu pull-right'>
          {_.map(props.actions, (action, key) => {
            return (
              user_has_role_permission(action.permission) && (
                <li key={key}>
                  <a href='#' onClick={action.callback}>
                    {action.label}
                  </a>
                </li>
              )
            )
          })}
        </ul>
      </div>
    </div>
  )
}
