import htmlTruncate from 'html-truncate'

export function cleanSpacesFromHtml(text: string) {
  if (text == null) {
    return text
  }

  text = text.trim().replace(/\r?\n|\r/g, '')
  text = text.replace(/>\s+</g, '><')
  text = text.replace(/\s{2,}/g, ' ')

  return text
}

export function truncateText(text: string, length: number = 300) {
  if (text == null) {
    return text
  }

  text = cleanSpacesFromHtml(text)
  text = text.replace(/<table.*\/table>/g, '<img src="/images/icon_tabular.png" title="Tabla" />') // Remove all tables

  let html = htmlTruncate('' + text, length, { keepImageTag: true })

  html = html.replace(/(min-)?width:.+?;/g, '')
  html = html.replace(/(min-)?height:.+?;/g, '')
  html = html.replace(/style=".+?"/g, '')

  return html.trim()
}
