import { Component } from 'react'

import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon'
import Label from 'semantic-ui-react/dist/commonjs/elements/Label'
import List from 'semantic-ui-react/dist/commonjs/elements/List'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'
import Dimmer from 'semantic-ui-react/dist/commonjs/modules/Dimmer/Dimmer'
import Loader from 'semantic-ui-react/dist/commonjs/elements/Loader/Loader'

export class AttachmentListItem extends Component<AttachmentListItemProps> {
  private static getFileExtension(filename: string): string {
    return filename.split('.').pop()
  }

  private static getFileIconUrl(file: string): string {
    const extension = AttachmentListItem.getFileExtension(file)

    const file_icon = function() {
      switch (extension) {
        case 'xls':
        case 'xlsx':
        case 'xlsm':
        case 'xlsxm':
          return 'excel'
        case 'doc':
        case 'docx':
          return 'word'
        case 'pdf':
          return 'pdf'
        case 'png':
        case 'jpg':
        case 'jpeg':
        case 'bmp':
        case 'gif':
          return 'image'
        case 'zip':
        case 'rar':
          return 'zip'
        default:
          return 'default'
      }
    }

    return public_path(`images/file_icons/${file_icon()}.png`)
  }

  changeStatus(e: React.SyntheticEvent, status: 'completado' | 'revisado') {
    e.preventDefault()

    const url: string = url_for(
      `proyecto/${this.props.attachment.id_proyecto}/adjunto/${this.props.attachment.id}/${status}`
    )
    $.blockUI({
      message:
        '<div class="ajax-loader-snake-32  _a-center"></div><h3 class="loading-block-text">Marcando archivo como ' +
        _.str.capitalize(status) +
        '.<br> Por favor Espere ...</h3>'
    })

    $.ajax(url, { type: 'POST', data: {}, dataType: 'json' })
      .done((res) => {
        this.props.onUpdate(res.response.record)
        $.unblockUI()
      })
      .fail(() => {
        $.pnotify({ text: 'El archivo no pudo ser actualizado.', type: 'error' })
        $.unblockUI()
      })
  }

  onRemove = (ev: React.SyntheticEvent) => {
    ev.preventDefault()

    const url: string = url_for(
      `proyecto/${this.props.attachment.id_proyecto}/adjunto/${this.props.attachment.id}/remove`
    )
    bootbox.warningConfirm(
      'Esta seguro de querer borrar este archivo?<br><br>Tenga en cuenta que el sistema intentar borrar automticamente los enlaces existentes a este archivo.',
      'Cancelar',
      'Aceptar',
      (r) => {
        if (r) {
          $.blockUI({
            message:
              '<div class="ajax-loader-snake-32  _a-center"></div><h3 class="loading-block-text">Borrando archivo.<br> Por favor Espere ...</h3>'
          })
          $.ajax(url, { type: 'POST', data: {}, dataType: 'json' })
            .done(() => {
              this.props.onDelete(this.props.attachment, () => $.unblockUI())
            })
            .fail(() => {
              $.pnotify({ text: 'El archivo no pudo ser borrado.', type: 'error' })
              $.unblockUI()
            })
        }
      }
    )
  }

  render() {
    const { attachment } = this.props

    let completadoComp
    if (attachment.fecha_completado) {
      completadoComp = (
        <div style={{ display: 'inline-block' }}>
          <strong className='title'>(P) </strong>
          <div
            style={{ display: 'inline-block', minWidth: 20, textAlign: 'center', marginRight: 2 }}
            title={`Preparado por: ${attachment.UpdateLog[0].User.name}`}
          >
            {attachment.UpdateLog[0].User.sigla}&nbsp;
          </div>
          <div style={{ display: 'inline-block' }} title={attachment.fecha_completado}>
            <span className='icon-moon-calendar' />{' '}
            {Date.create(attachment.fecha_completado).format('{year}-{MM}-{dd}')}
          </div>
        </div>
      )
    }

    let revisadoComp
    if (attachment.fecha_revisado) {
      revisadoComp = (
        <div style={{ display: 'inline-block' }}>
          <strong className='title'>(R) </strong>
          <div
            style={{ display: 'inline-block', minWidth: 20, textAlign: 'center', marginRight: 2 }}
            title={`Revisado por: ${attachment.RevisionLog[0].User.name}`}
          >
            {attachment.RevisionLog[0].User.sigla}&nbsp;
          </div>
          <div style={{ display: 'inline-block' }} title={attachment.fecha_revisado}>
            <span className='icon-moon-calendar' /> {Date.create(attachment.fecha_revisado).format('{year}-{MM}-{dd}')}
          </div>
        </div>
      )
    }

    let revisadoLink
    if (user_has_role([ 'PM', 'SR' ])) {
      if (attachment.fecha_completado) {
        revisadoLink = (
          <button style={{ marginLeft: '8px' }} onClick={(e) => this.changeStatus(e, 'revisado')} className='act'>
            <span className='icon-moon-checkmark-8' /> Revisar
          </button>
        )
      } else {
        revisadoLink = (
          <Popup
            trigger={
              <div className='button-wrapper'>
                <button style={{ marginLeft: '8px' }} className='act disabled'>
                  <span className='icon-moon-checkmark-8' /> Revisar
                </button>
              </div>
            }
            content='Esta archivo adjunto debe estar primero en estado COMPLETADO, para poder ser marcado como REVISADO'
            header='Marcar como Revisado | Opcin deshabilitada'
            className='disable-text'
            on='hover'
          />
        )
      }
    }

    return (
      <li key={attachment.id} style={{ marginBottom: 2 }}>
        <div style={{ background: '#f5f9f8', borderRadius: 6, border: '1px solid #aecec7' }}>
          <div className='col-md-1' style={{ marginTop: 5, borderRight: '1px inset #d7e1e2' }}>
            <img src={AttachmentListItem.getFileIconUrl(attachment.filename)} alt='Icono' />
          </div>
          <div className='col-md-11' style={{ padding: '3px 10px' }}>
            <div
              className='noqtip'
              title={attachment.filename}
              style={{
                fontWeight: 'bold',
                fontSize: '12px',
                maxWidth: 400,
                whiteSpace: 'nowrap',
                overflow: 'hidden',
                textOverflow: 'ellipsis'
              }}
            >
              {attachment.filename}
            </div>
            <div style={{ fontSize: '12px' }}>
              <span
                style={{ fontStyle: 'italic' }}
                className='noqtip'
                title={`Peso del archivo: ${attachment.filesize_human}`}
              >
                {attachment.filesize_human}
              </span>{' '}
              |
              <div className='attachment-actionsbar'>
                <div className='section-a'>
                  <a
                    href={public_path(`uploads/proyecto_adjunto/${attachment.filepath}`)}
                    target='_blank'
                    className='act'
                    style={{ color: '#777' }}
                  >
                    <span className='icon-moon-download' style={{ fontSize: '10px' }} /> Descargar
                  </a>
                </div>

                {user_has_role([ 'PM', 'SR', 'JR' ]) && (
                  <div className='section-a'>
                    <a href='#' className='act' onClick={this.onRemove}>
                      <span className='icon-tc-trash' /> Borrar
                    </a>
                  </div>
                )}

                <div className='section-a' style={{ minWidth: 90 }}>
                  {attachment.fecha_completado ? (
                    <Popup trigger={completadoComp} position='bottom center'>
                      <Popup.Header content='Preparado por' />
                      <Popup.Content>
                        <table className='table nowrap'>
                          <tbody>
                            {attachment.UpdateLog.map((item, k) => {
                              return (
                                <tr key={k}>
                                  <td>
                                    <Icon name='user circle' color='green' /> {item.User.name} ({item.User.sigla})
                                  </td>
                                  <td>
                                    <em>{Date.create(item.ts).format('{year}-{MM}-{dd}')}</em>
                                  </td>
                                </tr>
                              )
                            })}
                          </tbody>
                        </table>
                      </Popup.Content>
                    </Popup>
                  ) : (
                    user_has_role([ 'PM', 'SR', 'JR' ]) && (
                      <button
                        style={{ marginLeft: '8px' }}
                        onClick={(e) => this.changeStatus(e, 'completado')}
                        className='act'
                      >
                        <span className='icon-moon-checkmark-8' /> Completar
                      </button>
                    )
                  )}
                </div>

                <div className='section-a'>
                  {attachment.fecha_revisado && (
                    <Popup trigger={revisadoComp} position='bottom center'>
                      <Popup.Header content='Revisado por' />
                      <Popup.Content>
                        <table className='table nowrap'>
                          <tbody>
                            {attachment.RevisionLog.map((item, k) => {
                              return (
                                <tr key={k}>
                                  <td>
                                    <Icon name='user circle' color='olive' /> {item.User.name} ({item.User.sigla})
                                  </td>
                                  <td>
                                    <em>{Date.create(item.ts).format('{year}-{MM}-{dd}')}</em>
                                  </td>
                                </tr>
                              )
                            })}
                          </tbody>
                        </table>
                      </Popup.Content>
                    </Popup>
                  )}
                  {revisadoLink}
                </div>
              </div>
            </div>
            <div />
          </div>
          <div className='clearfix' />
        </div>
      </li>
    )
  }
}

export class AttachmentListPanel extends Component<AttachmentListPanelProps> {
  static defaultProps = {
    isLoading: false
  }
  render() {
    let { isLoading, attachments } = this.props

    return (
      <div className='attachment-list-panel dimmable' style={{ minHeight: 40 }}>
        {isLoading && (
          <Dimmer inverted active>
            <Loader inverted active className='elastic grey' />
          </Dimmer>
        )}
        <h3 className='panel-title'>
          Adjuntos:
          <Label circular color='green'>
            {_.size(attachments)}
          </Label>
        </h3>
        {_.size(attachments) > 0 && (
          <List divided className='attachment-list'>
            {_.map(attachments, (attachment: ProyectoAttachment, i) => {
              return (
                <AttachmentListItem
                  key={attachment.id}
                  attachment={attachment}
                  onUpdate={this.props.onUpdateItem}
                  onDelete={this.props.onDeleteItem}
                />
              )
            })}
          </List>
        )}
      </div>
    )
  }
}

export function cleanAttachmentFrom(value: string, adjunto: ProyectoAttachment) {
  const h = public_path(`uploads/proyecto_adjunto/${adjunto.filepath}`, true).replace(/\./g, '.').replace(/\\/g, '/')
  const t = '<a .*href="' + h + '"(.+?)/a>\\s*(<br\\s?/?>)?'
  const re = new RegExp(t, 'g')

  return value.replace(re, '')
}
