import { PureComponent, SyntheticEvent } from 'react'
import { HallazgoUtils } from '../utils_hallazgo'
import { AuthorIconLogDefaultWrapper } from '../../global/AuthorIconLog'
import { HallazgoModalEdit } from './hallazgo_edit'
import { MenuElementListPanel, MenuElementListActions } from './menuitem'
import { StatusDivBlock } from '../../global/StatusButtonAction'
import { truncateText } from '../utils_truncate'

export class HallazgoList extends PureComponent<DefaultMenuItemListProps<ProyectoHallazgo>> {
  render() {
    if (this.props.items === null) {
      return (
        <div style={{ height: 300, position: 'relative' }}>
          <div className='ui active inverted text loader elastic olive'>Cargando Listado de Hallazgos</div>
        </div>
      )
    } else if (_.size(this.props.items) == 0) {
      return <div className='empty nocontent'>0 Hallazgos</div>
    } else {
      const { items } = this.props
      return (
        <MenuElementListPanel>
          {_.map(items, (item) => {
            return (
              <div key={item.id} className='listitem hallazgo-listitem'>
                <HallazgoListItem
                  item={item}
                  moduleParams={this.props.moduleParams}
                  onSave={this.props.onSaveItem}
                  onClickDelete={this.props.onClickDeleteItem}
                />
              </div>
            )
          })}
        </MenuElementListPanel>
      )
    }
  }
}

type HallazgoListItemProps = {
  moduleParams: any
  item: ProyectoHallazgo
  onSave: (item: ProyectoHallazgo, changes: any, blockingText?: string) => void
  onClickDelete: (item: ProyectoHallazgo) => void
}

export class HallazgoListItem extends PureComponent<HallazgoListItemProps, { isModalEdit: boolean }> {
  state = {
    isModalEdit: false
  }

  renderDescripcion() {
    const { item } = this.props
    let html = truncateText(item.hallazgo)

    return <div dangerouslySetInnerHTML={{ __html: html }} />
  }

  renderEstadoImplementacion() {
    const { item } = this.props

    if (item.implementado == null || '' + item.implementado == '') {
      return 'Pendiente'
    } else if (item.implementado == '0') {
      return 'En Proceso'
    } else {
      return 'Hecho'
    }
  }

  onClickEdit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.setState({ isModalEdit: true })
  }

  onCloseEdit = () => {
    this.setState({ isModalEdit: false })
  }

  onClickDelete = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onClickDelete(this.props.item)
  }

  onSave = (item: ProyectoHallazgo, changes: any) => {
    let blockingText = null
    if (_.isEqual(changes, { status: 'COMPLETADO' })) {
      blockingText = 'Marcando como COMPLETADO'
    } else if (_.isEqual(changes, { status: 'REVISADO' })) {
      blockingText = 'Marcando como REVISADO'
    }

    this.props.onSave(item, changes, blockingText)
  }

  render() {
    const { item } = this.props

    return (
      <div>
        <MenuElementListActions
          actions={{
            edit: { label: 'Editar', callback: this.onClickEdit, permission: 'hallazgo_edit' },
            delete: { label: 'Eliminar', callback: this.onClickDelete, permission: 'hallazgo_delete' }
          }}
        />
        <div onClick={() => this.setState({ isModalEdit: true })}>
          <div className='row r1'>
            <div className='pull-left r1_1'>
              <span className='title-h1'>{HallazgoUtils.formatName(item.name)}</span>
              <AuthorIconLogDefaultWrapper model={item} size='small' />
              <div className='r1_2'>
                <strong>Implementacin: </strong> {this.renderEstadoImplementacion()}
              </div>
            </div>
            <StatusDivBlock wrapperClassName='pull-right text-mini' status={item.status} className='mini' hideLabel />
          </div>
          <div className='row'>
            <div className='editor-preview'>{this.renderDescripcion()}</div>
          </div>
          <div className='row' />
        </div>
        {this.state.isModalEdit && (
          <HallazgoModalEdit
            item={item}
            moduleParams={this.props.moduleParams}
            onSave={this.onSave}
            onClickDelete={this.onClickDelete}
            onClose={this.onCloseEdit}
          />
        )}
      </div>
    )
  }
}
