import { PureComponent, SyntheticEvent } from 'react'
import { invoke } from 'lodash'

type SidebarPanelHeaderProps = {
  title: string
  onClose: (ev: SyntheticEvent) => void
  onMount?: () => void
}

export class SidebarPanelHeader extends PureComponent<SidebarPanelHeaderProps> {
  componentDidMount() {
    invoke(this.props, 'onMount')
  }
  render() {
    return (
      <header>
        <a href='#' className='close-link' onClick={this.props.onClose}>
          <i className='ui icon close green' />
        </a>
        <div className='title'>{this.props.title}</div>
      </header>
    )
  }
}
