import { SyntheticEvent, PureComponent } from 'react'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import { HallazgoModal } from './hallazgo_modal'

type HallazgoModalEditProps = {
  moduleParams: any
  item: ProyectoHallazgo
  onSave: (item: ProyectoHallazgo, data: ProyectoHallazgo) => void
  onClickDelete: (ev: SyntheticEvent) => void
  onClose: () => void
}
export class HallazgoModalEdit extends PureComponent<HallazgoModalEditProps> {
  onSave = (data: ProyectoHallazgo) => {
    this.props.onSave(this.props.item, data)
  }

  render() {
    return (
      <Modal
        open
        dimmer='inverted'
        className='modal-ab modal-sidebarmenuitem'
        size='large'
        closeIcon
        onClose={this.props.onClose}
      >
        <Modal.Header>Editar Hallazgo</Modal.Header>

        <HallazgoModal
          moduleParams={this.props.moduleParams}
          item={this.props.item}
          isEditable={user_has_role_permission('hallazgo_edit')}
          onSave={this.onSave}
          onClickDelete={this.props.onClickDelete}
        />
      </Modal>
    )
  }
}
