import { PureComponent } from 'react'

import { BaseMenuElement } from './menuitem'
import { MenuItemNew } from './menuitem__new'
import { AjusteModal } from './ajuste_modal'
import { AjusteList } from './ajuste_list'
import MenuItemAjusteContext from './menuitem_ajuste_context'
import { getNombreCuenta } from '../utils_ajuste'

type MenuItemAjusteState = {
  items: ProyectoAjusteGroup[]
}

declare var app: any

export class MenuItemAjuste extends PureComponent<DefaultMenuElementProps<ProyectoAjusteGroup>, MenuItemAjusteState> {
  cuentas: ProyectoAjusteCuenta[] = null

  state: MenuItemAjusteState = {
    items: null
  }

  parseAjustes = (input: any[]): ProyectoAjusteGroup[] => {
    const self = this
    let ajustes = _.groupBy(input, 'idx')

    return _.map(ajustes, function(_row, idx) {
      const row = _.map(_row, function(item) {
        return {
          id: item.id,
          cuenta: item.cuenta,
          debe: item.col == 'DEBE' ? item.valor : '',
          haber: item.col == 'HABER' ? item.valor : '',
          nombre_cuenta: self.getNombreCuenta(item.cuenta)
        }
      })

      return Object.assign(
        {},
        {
          idx,
          items: row,
          status: _row[0].status,
          descripcion: _row[0].description,
          id_visita: _row[0].id_proyecto_visita
        },
        _.pick(_row[0], [ 'fecha_completado', 'aplicado', 'fecha_revision', 'UpdateLog', 'RevisionLog' ])
      )
    })
  }

  getNombreCuenta(cuenta: string) {
    return getNombreCuenta(this.cuentas, cuenta)
  }

  onCreateItem = (data: any) => {
    return new Promise<null>((resolve) => {
      $.blockUI(create_blockUI_loading_message('Creando ajuste'))
      const { id_visita, id_paso } = this.props.moduleParams
      $.post(url_for(`proyecto/${app.vars.idproyecto}/visita/${id_visita}/cifra-ajuste`), {
        changes: data,
        id_paso,
        v: 2
      }).done((res) => {
        this.setState((prevState) => {
          const items = [ ...prevState.items ]
          items.push(this.parseAjustes(res.response.ajustes)[0])

          return { items }
        })

        $.unblockUI()
        resolve()
      })
    })
  }

  onMountPanel = () => {
    if (this.state.items === null) {
      const { id_visita } = this.props.moduleParams
      $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/visita/${id_visita}/cifra-ajuste`)).done((res) => {
        this.cuentas = _.isEmpty(res.response.cuentas) ? [] : res.response.cuentas
        app.vars.error_tolerable[id_visita] = res.response.error_tolerable

        this.setState({
          items: _.isEmpty(res.response.ajustes) ? [] : this.parseAjustes(res.response.ajustes)
        })
      })
    }
  }

  onSaveItem = (item: ProyectoAjusteGroup, changes: any, blockingText?: string) => {
    return new Promise<ProyectoAjusteGroup>((resolve) => {
      const { id_visita, id_paso } = this.props.moduleParams
      if (blockingText) {
        setTimeout(() => $.blockUI(create_blockUI_loading_message(blockingText)), 1000 * 0.1)
      }

      $.post(url_for(`proyecto/${app.vars.idproyecto}/visita/${id_visita}/cifra-ajuste`), {
        idx: item.idx,
        changes,
        id_paso,
        v: 2
      }).done((res) => {
        const ajustes = this.parseAjustes(res.response.ajustes)
        this.setState((prevState) => {
          const items = _.map(prevState.items, (item) => {
            if (item.idx == ajustes[0].idx) {
              return ajustes[0]
            } else {
              return item
            }
          })
          return { items }
        })

        if (blockingText) {
          $.unblockUI()
        }

        resolve(ajustes[0])
      })
    })
  }

  onClickDeleteItem = (item: ProyectoAjusteGroup) => {
    bootbox.warningConfirm(
      `Desea eliminar el <strong>Ajuste #${item.idx}</strong>,
      el cual contiene un total de ${_.size(item.items)} registros contables?`,
      'No',
      'S, Eliminar',
      (r) => {
        if (!r) {
          return
        }

        const { id_visita } = this.props.moduleParams

        $.blockUI(create_blockUI_loading_message('Eliminando ajustes'))
        $.ajax(url_for(`proyecto/${app.vars.idproyecto}/visita/${id_visita}/cifra-ajuste/${item.idx}`), {
          type: 'delete'
        }).done((res) => {
          this.setState((prevState) => {
            const items = _.reject(prevState.items, { idx: item.idx })

            return { items }
          })

          $.unblockUI()
        })
      }
    )
  }

  renderContent() {
    const itemNew: ProyectoAjusteGroup = {
      idx: null,
      status: 'NUEVO',
      descripcion: '',
      id_visita: this.props.moduleParams.id_visita,
      items: _.fill(new Array(2), { cuenta: '', debe: null, haber: null, nombre_cuenta: '' }, 0, 2)
    }

    return (
      <div className='content'>
        {_.size(this.cuentas) > 0 && (
          <MenuItemNew
            moduleParams={this.props.moduleParams}
            buttonText='Crear Ajuste'
            headerText='Nuevo Ajuste'
            isEditable={user_has_role_permission('ajustes_edit')}
            onSave={this.onCreateItem}
            item={itemNew}
            contentAs={AjusteModal}
          />
        )}
        {_.isArray(this.cuentas) && _.size(this.cuentas) == 0 ? (
          <div className='ui message error' style={{ marginTop: 40 }}>
            <i className='icon warning sign' />
            <strong>Advertencia: </strong> Al parecer an no se han cargado sumarias a esta visita.
          </div>
        ) : (
          <AjusteList
            items={this.state.items}
            moduleParams={this.props.moduleParams}
            isEditable={es_proyecto_editable() && user_has_role_permission('ajustes_edit')}
            onSaveItem={this.onSaveItem}
            onClickDeleteItem={this.onClickDeleteItem}
          />
        )}
      </div>
    )
  }

  render() {
    return (
      <BaseMenuElement
        name='ajuste'
        counter={this.state.items === null ? this.props.counter : _.size(this.state.items)}
        icon='ajustes'
        title='Ajustes'
        open={this.props.open}
        onToggle={this.props.onOpen}
        onMountPanel={this.onMountPanel}
      >
        <MenuItemAjusteContext.Provider value={this.cuentas}>{this.renderContent()}</MenuItemAjusteContext.Provider>
      </BaseMenuElement>
    )
  }
}
