import { Component, SyntheticEvent } from 'react'
import TabPaneVisita from './TabPaneVisita'
import { AuthorIconLog } from './../global/AuthorIconLog'

import Label from 'semantic-ui-react/dist/commonjs/elements/Label'

import { Tabs, TabList, Tab, PanelList, Panel } from 'react-tabtab'

import { SortableContainer, SortableElement, SortableHandle } from 'react-sortable-hoc'
import HojaTrabajoContext from './HojaTrabajoContext'

declare var app: any

declare type HojaTrabajoAppPeriodoState = {
  activeIndex: number
  newVisitaOpen: boolean
  hoja_trabajo: HojaTrabajo
}

export default class AppPeriodo extends Component<HojaTrabajoAppProps, HojaTrabajoAppPeriodoState> {
  constructor(props: HojaTrabajoAppProps) {
    super(props)

    const visita_current = _.find(this.props.hoja_trabajo.visitas, { is_current: true })
    const activeIndex = visita_current ? visita_current.index : null

    if (visita_current) {
      app.vars.current_idvisita = visita_current.id_visita
    }

    this.state = {
      activeIndex,
      newVisitaOpen: false,
      hoja_trabajo: this.props.hoja_trabajo
    }
  }

  handleClick = (index: number) => {
    this.setState({ activeIndex: index })
    const visita = _.find(this.state.hoja_trabajo.visitas, { index })
    app.vars.current_idvisita = visita.id_visita
    $(`#table-visita-instrucciones-${visita.id}`).floatThead('reflow')
  }

  onChangeVisita = (visita: ProyectoPasoVisita) => {
    this.setState((state: HojaTrabajoAppState) => {
      let hoja_trabajo = Object.assign({}, state.hoja_trabajo)
      hoja_trabajo.visitas = _.map(hoja_trabajo.visitas, (item) => {
        if (item.index + '' == visita.index + '') {
          return visita
        } else {
          return item
        }
      })
      return { hoja_trabajo }
    })
  }

  onChangeApp = (data: any) => {
    this.setState((state: HojaTrabajoAppState) => {
      let hoja_trabajo = Object.assign({}, state.hoja_trabajo, data)

      return { hoja_trabajo }
    })
  }

  onClickActionElement = (ev: SyntheticEvent, action: 'CREATE' | 'RENAME' | 'DELETE', visita?: ProyectoPasoVisita) => {
    ev.preventDefault()
    switch (action) {
      case 'CREATE':
        bootbox.prompt(
          { header: 'Nuevo Periodo', content: '<div>Escriba el nombre del periodo que desea crear.</div>' },
          'Cancelar',
          'Crear',
          (r: string) => {
            if (!r || _.str.trim(r) == '') {
              return
            }
            const url = this.getUrlFor('periodo')

            $.blockUI(create_blockUI_loading_message('Creando Nuevo Periodo'))

            $.ajax(url, {
              type: 'POST',
              data: { id_paso: this.state.hoja_trabajo.paso.id, name: _.str.trim(r) },
              dataType: 'json'
            }).done((res) => {
              if (!res.hasErrors) {
                this.setState((state) => {
                  let hoja_trabajo = state.hoja_trabajo
                  hoja_trabajo.visitas[res.response.id_visita] = res.response

                  return { hoja_trabajo, activeIndex: res.response.index }
                })
              } else {
                bootbox.alert(res.message)
              }
              $.unblockUI()
            })
          },
          ''
        )
        $('.bootbox.modal').addClass('modal-ab')
        break
      case 'RENAME':
        bootbox.prompt(
          { header: 'Renombrar Periodo', content: 'Escriba el nombre que desea asignarle a este periodo.' },
          'Cancelar',
          'Aceptar',
          (r: string) => {
            if (!r || _.str.trim(r) == '') {
              return
            }

            $.blockUI(create_blockUI_loading_message('Guardando cambios'))
            const url = url_for(
              `proyecto/${this.state.hoja_trabajo.proyecto.id}/master_data/paso-visita/rename/${visita.id_visita}`
            )

            $.ajax(url, {
              type: 'POST',
              data: { name: _.str.trim(r) },
              dataType: 'json'
            }).done((res) => {
              if (!res.hasErrors) {
                this.setState((state) => {
                  let hoja_trabajo = state.hoja_trabajo
                  hoja_trabajo.visitas = _.map(hoja_trabajo.visitas, function(item) {
                    if (item.id_visita == visita.id_visita) {
                      item.label = _.str.trim(r)
                    }
                    return item
                  })

                  return { hoja_trabajo }
                })
              }
              $.unblockUI()
            })
          },
          visita.label
        )
        $('.bootbox.modal').addClass('modal-ab')
        break
      case 'DELETE':
        bootbox.warningConfirm('Desea eliminar este periodo?', 'No', 'S, Eliminar periodo', (r) => {
          if (r) {
            $.blockUI(create_blockUI_loading_message(`Eliminando Periodo "${visita.label}"`))
            const url = url_for(
              `proyecto/${this.state.hoja_trabajo.proyecto.id}/master_data/paso-visita/delete/${visita.id_visita}`
            )

            $.ajax(url, {
              type: 'POST',
              dataType: 'json'
            }).done((res) => {
              $.unblockUI()
              if (!res.hasErrors) {
                this.setState((state) => {
                  let hoja_trabajo = state.hoja_trabajo
                  hoja_trabajo.visitas = _.reject(hoja_trabajo.visitas, function(item) {
                    return item.id_visita == visita.id_visita
                  })

                  let activeIndex = state.activeIndex
                  if (activeIndex == visita.index) {
                    activeIndex = _.head(hoja_trabajo.visitas).index
                  }

                  return { hoja_trabajo, activeIndex }
                })
              }
            })
          }
        })
    }
  }

  static shortVisitaName(visita: string): string {
    const transformations: { [s: string]: string } = {
      Enero: 'Ene',
      Febrero: 'Feb',
      Marzo: 'Mar',
      Abril: 'Abr',
      Mayo: 'May',
      Junio: 'Jun',
      Julio: 'Jul',
      Agosto: 'Ago',
      Septiembre: 'Sept',
      Octubre: 'Oct',
      Noviembre: 'Nov',
      Diciembre: 'Dic'
    }

    _.forEach(transformations, function(to, from) {
      visita = visita.replace(new RegExp(from, 'gi'), to)
    })

    return visita
  }

  getUrlFor(urlToAppend: string) {
    return url_for(
      `proyecto/${this.props.hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${this.props.hoja_trabajo.paso
        .id}/${urlToAppend}`
    )
  }

  isAllowedAsociarVisita() {
    if (!user_has_role_permission('paso_asociar_visita')) {
      return false
    }

    if (!this.state.hoja_trabajo.proyecto.isOpen) {
      return false
    }
  }

  renderTab(visita: ProyectoPasoVisita, k: number) {
    let status_label = null

    if (visita.status) {
      status_label = (
        <Label
          style={{ backgroundColor: visita.status_color }}
          className={`visita-status visita-status-${_.str.underscored(visita.status)}`}
          size='mini'
        >
          {visita.status}
        </Label>
      )
    }

    const alertTextRevisado =
      visita.fecha_completado &&
      visita.fecha_revision &&
      Date.create(visita.fecha_completado) > Date.create(visita.fecha_revision)
        ? 'Modificado despues de Revisado'
        : null

    return (
      <Tab key={visita.id} className='item tab-nav-item visita-tab'>
        <span className='tabname'>{AppPeriodo.shortVisitaName(visita.label)}</span>
        <div className='author-icon-log-wrapper'>
          <AuthorIconLog log={visita.UpdateLog} name='Preparado por' label='P' iconColor='green' />
          <AuthorIconLog
            log={visita.RevisionLog}
            name='Revisado por'
            label='R'
            iconColor='olive'
            alertText={alertTextRevisado}
          />
        </div>
        {status_label}
      </Tab>
    )
  }
  renderPanel(visita: ProyectoPasoVisita, k: number) {
    return (
      <Panel key={visita.id}>
        <TabPaneVisita
          idx={visita.index}
          active={this.state.activeIndex == visita.index}
          visita={visita}
          type='PERIODO'
          isEditable={this.state.hoja_trabajo.proyecto.isOpen}
          onChange={this.onChangeVisita}
          onChangeApp={this.onChangeApp}
        />
      </Panel>
    )
  }

  renderModalContent() {
    if (!this.state.hoja_trabajo.proyecto.isOpen) {
      return null
    }

    const self = this
    let visitas: ProyectoPasoVisita[] = _.values(self.state.hoja_trabajo.visitas)
    visitas = _.sortBy(visitas, 'position')

    const DragHandle = SortableHandle(() => <span className='sort-handler drag-icon' />)

    const SortElement = SortableElement(({ visita }: { visita: ProyectoPasoVisita }) => {
      return (
        <li className='list-group-item visita-li' id={`visita-li-${visita.id}`}>
          {user_has_role_permission('hoja_trabajo_edit') && <DragHandle />}
          <span>{visita.label}</span>

          <div className='pull-right'>
            {user_has_role_permission('hoja_trabajo_edit') && [
              <a
                href='#'
                key='rename'
                className='btn-mini btn-link'
                onClick={(ev) => this.onClickActionElement(ev, 'RENAME', visita)}
              >
                Cambiar nombre
              </a>,
              '|',
              <a
                href='#'
                key='delete'
                className='btn-link btn-mini delete'
                onClick={(ev) => this.onClickActionElement(ev, 'DELETE', visita)}
              >
                Eliminar
              </a>
            ]}
          </div>
        </li>
      )
    })

    const SortableList = SortableContainer(({ visitas }: { visitas: ProyectoPasoVisita[] }) => {
      return (
        <ul id='paso-periodo-sortlist' className='list-group compact' style={{ marginLeft: 0 }}>
          {_.map(visitas, (visita, index: number) => (
            <SortElement visita={visita} key={`visita-li-${visita.id}`} index={index} />
          ))}
        </ul>
      )
    })

    const onSortEnd = ({ oldIndex, newIndex }: { oldIndex: number; newIndex: number }) => {
      const arrayMoveMutate = (array: any[], from: number, to: number) => {
        array.splice(to < 0 ? array.length + to : to, 0, array.splice(from, 1)[0])
      }

      const arrayMove = (array: any[], from: number, to: number) => {
        array = array.slice()
        arrayMoveMutate(array, from, to)
        return array
      }

      this.setState(
        (state) => {
          let hoja_trabajo = state.hoja_trabajo
          let visitas: ProyectoPasoVisita[] = _.values(hoja_trabajo.visitas)
          visitas = _.sortBy(visitas, 'position')
          let sorted = arrayMove(visitas, oldIndex, newIndex)
          sorted = _.map(sorted, function(item, k: number) {
            item.position = k
            return item
          })
          hoja_trabajo.visitas = sorted

          return { hoja_trabajo }
        },
        () => {
          let neworder: { [s: number]: string } = {}
          _.forEach(this.state.hoja_trabajo.visitas, function(item) {
            neworder[item.position] = item.id_visita
          })

          const url = url_for(`proyecto/${this.state.hoja_trabajo.proyecto.id}/master_data/paso-visita/sort`)
          $.ajax(url, {
            type: 'POST',
            data: { sort: neworder },
            dataType: 'json'
          })
        }
      )
    }

    return (
      <div style={{ padding: '5px 20px' }}>
        <p className='description-text'>Listado de periodos asociados a este paso.</p>

        <SortableList
          visitas={visitas}
          lockAxis='y'
          helperClass='sortable_placeholder'
          useDragHandle
          onSortEnd={onSortEnd}
          helperContainer={() => document.getElementById('paso-periodo-sortlist')}
        />

        <div style={{ margin: 'auto', textAlign: 'center' }}>
          <button className='btn btn-primary' onClick={(ev) => this.onClickActionElement(ev, 'CREATE')}>
            <span className='icon-plus' /> Nuevo Periodo
          </button>
        </div>
        <br />
      </div>
    )
  }

  render() {
    const self = this
    let visitas: ProyectoPasoVisita[] = _.values(self.state.hoja_trabajo.visitas)

    if (_.size(visitas) == 0) {
      return (
        <div id='visita-tabs' className={`paso-code-${this.state.hoja_trabajo.paso.code} paso-periodo`}>
          <div className='alert alert-block nocontent'>
            <h3>Sin Periodos</h3>
            <p>
              <span className='icon-warning-sign' />Este paso aun no tiene periodos creados.
            </p>
            {user_has_role_permission('hoja_trabajo_edit') && (
              <button
                className='btn btn-primary'
                onClick={(ev) => this.onClickActionElement(ev, 'CREATE')}
                style={{ marginTop: '30px' }}
              >
                <span className='icon-plus' /> Nuevo periodo
              </button>
            )}
          </div>
        </div>
      )
    }

    visitas = _.sortBy(visitas, 'position')

    return (
      <HojaTrabajoContext.Provider value={{ hoja_trabajo: this.state.hoja_trabajo, update: this.onChangeApp }}>
        <div id='visita-tabs' className={`paso-code-${this.state.hoja_trabajo.paso.code} paso-periodo`}>
          <Tabs
            activeIndex={this.state.activeIndex}
            onTabChange={this.handleClick}
            showModalButton={this.state.hoja_trabajo.proyecto.isOpen}
            modalHeader='Administracin de Periodos'
            modalContent={this.renderModalContent()}
          >
            <TabList className='ui tabular menu'>
              {_.map(visitas, (visita, k) => {
                visita.HojaTrabajo = self.state.hoja_trabajo
                return this.renderTab(visita, k)
              })}
            </TabList>
            <PanelList>
              {_.map(visitas, (visita, k) => {
                visita.HojaTrabajo = self.state.hoja_trabajo
                return this.renderPanel(visita, k)
              })}
            </PanelList>
          </Tabs>
        </div>
      </HojaTrabajoContext.Provider>
    )
  }
}
