import { PureComponent, Fragment, SyntheticEvent } from 'react'
import { MenuItemNewHeader } from './menuitem__new'
import EditorPreview from '../../global/EditorPreview'
import InputAutoNumeric from '../../global/InputAutoNumeric'
import MenuItemAjusteContext from './menuitem_ajuste_context'
import Autocomplete from 'react-autocomplete'
import { getNombreCuenta } from '../utils_ajuste'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup'
import { StatusButtonGroupActions } from '../../global/StatusButtonAction'
import cx from 'classnames'
import { GenericTooltip } from '../../global/Tooltip'

declare var app: any

type AjusteModalProps = {
  moduleParams: any
  item: ProyectoAjusteGroup
  isEditable: boolean
  onSave: (changes: any, blockingText?: string) => void
  onClickDelete?: (ev: SyntheticEvent) => void
}

type AjusteModalState = {
  descripcion: string
  items: ProyectoAjuste[]
  status: ProyectoModuleStatusEnum
  aplicado: number
}

export class AjusteModal extends PureComponent<AjusteModalProps, AjusteModalState> {
  constructor(props: AjusteModalProps) {
    super(props)

    this.state = {
      descripcion: props.item.descripcion,
      items: props.item.items || [],
      status: props.item.status,
      aplicado: props.item.aplicado
    }
  }

  isNewItem() {
    return !this.props.item.idx
  }

  esPlanillaDescuadrada() {
    const { items } = this.state

    if (!items) {
      return false
    }

    const totalD = _.reduce(items, (acc, curr) => num(curr.debe) + acc, 0)
    const totalH = _.reduce(items, (acc, curr) => num(curr.haber) + acc, 0)

    return totalD != totalH
  }

  validateItems() {
    return (
      _.size(this.state.items) > 0 &&
      !_.any(this.state.items, (item) => {
        return item['cuenta'] == '' || item['nombre_cuenta'] == '' || (!item['debe'] && !item['haber'])
      })
    )
  }

  onChangeAplicado = (ev: SyntheticEvent) => {
    this.setState({ aplicado: (ev.target as any).value })
    if (!this.isNewItem()) {
      this.props.onSave({ aplicado: (ev.target as any).value })
    }
  }

  onChangeDescripcion = (ckeditor: CKEDITOR.editor) => {
    this.setState({ descripcion: ckeditor.getData() })
    if (!this.isNewItem()) {
      this.props.onSave({ descripcion: ckeditor.getData() })
    }
  }

  onAddItem = () => {
    this.setState((prevState) => {
      const items = [ ...prevState.items, createAjusteEmpty() ]

      return { items }
    })
  }

  onDeleteItem = (item: ProyectoAjuste, itemIndex: number) => {
    this.setState(
      (prevState) => {
        const items = _.reject(prevState.items, (_item, k: number) => k == itemIndex)

        return { items }
      },
      () => {
        if (!this.isNewItem()) {
          this.props.onSave({ items: this.state.items })
        }
      }
    )
  }

  onUpdateItem = (item: ProyectoAjuste, itemIndex: number, notify: boolean = true) => {
    this.setState(
      (prevState) => {
        let items
        if (typeof prevState.items[itemIndex] === 'undefined') {
          items = [ ...prevState.items ]
          items.push(item)
        } else {
          items = _.map(prevState.items, (_item, k) => {
            if (k == itemIndex) {
              return Object.assign({}, _item, item)
            } else {
              return _item
            }
          })
        }

        return { items }
      },
      () => {
        if (notify && !this.isNewItem()) {
          const blockingText = _.any(this.state.items, (_item) => !_item.id) ? 'Guardando cambios' : null
          this.props.onSave({ items: this.state.items }, blockingText)
        }
      }
    )
  }

  _onCreate(changes: any, blockingText?: string) {
    if (!this.validateItems()) {
      bootbox.alert('Ingrese el codigo de la cuenta y valor del ajuste para cada una de las filas del ajuste contable.')
      return
    }
    this.props.onSave(Object.assign({}, this.state, changes), blockingText)
  }

  onCreate = () => {
    this._onCreate({ status: 'EN PROCESO' }, 'Creando Ajuste')
  }

  onCreateAndCompletado = () => {
    this._onCreate({ status: 'COMPLETADO' }, 'Creando Ajuste')
  }

  onCompletado = () => {
    this.props.onSave({ status: 'COMPLETADO' }, 'Marcando como COMPLETADO')
  }

  onRevisado = () => {
    this.props.onSave({ status: 'REVISADO' }, 'Marcando como REVISASO')
  }

  renderItemHeader() {
    const item = Object.assign(
      { label: this.props.item.idx ? `Ajuste #${this.props.item.idx}` : 'Ajuste Nuevo' },
      this.props.item
    )

    return <MenuItemNewHeader item={item} />
  }

  renderRows() {
    let items = this.state.items

    return (
      <Fragment>
        <MenuItemAjusteContext.Consumer>
          {(cuentas) =>
            _.map(items, (row, k) => {
              return (
                <AjusteTableRow
                  item={row}
                  key={k}
                  itemIndex={k}
                  cuentas={cuentas}
                  isEditable={this.props.isEditable}
                  canRemove={_.size(items) > 1}
                  onUpdate={this.onUpdateItem}
                  onClickDelete={this.onDeleteItem}
                />
              )
            })}
        </MenuItemAjusteContext.Consumer>
      </Fragment>
    )
  }

  renderActions() {
    const { item } = this.props

    const esPlanillaDescuadrada = this.esPlanillaDescuadrada()

    let revisadoDisableText = null
    if (esPlanillaDescuadrada) {
      revisadoDisableText =
        'No es posible guardar los cambios realizados en este ajuste mientras la planilla est descuadrada.'
    } else if (!item.fecha_completado) {
      revisadoDisableText = 'Debe estar primero en estado COMPLETADO, para poder marcarse como REVISADO.'
    }

    const rightActions = !this.isNewItem() && (
      <button className='ui button compact negative left floated' onClick={this.props.onClickDelete}>
        <i className='icon trash' /> Eliminar
      </button>
    )

    return this.isNewItem() ? (
      <div className='actions ab-status-actions'>
        <Popup
          content='No es posible guardar este ajuste mientras la planilla est descuadrada.'
          disabled={!esPlanillaDescuadrada}
          className='disable-text'
          trigger={
            <div className='button-wrapper'>
              <button className='ui button compact green' onClick={this.onCreate} disabled={esPlanillaDescuadrada}>
                Crear
              </button>
            </div>
          }
        />

        <Popup
          content='No es posible guardar este ajuste mientras la planilla est descuadrada.'
          disabled={!esPlanillaDescuadrada}
          className='disable-text'
          trigger={
            <div className='button-wrapper'>
              <button
                className='ui button compact green'
                onClick={this.onCreateAndCompletado}
                disabled={esPlanillaDescuadrada}
              >
                Crear y Completar
              </button>
            </div>
          }
        />
      </div>
    ) : (
      <Fragment>
        {rightActions}
        <StatusButtonGroupActions
          completado={{
            permission: 'hallazgo_completado',
            onClick: this.onCompletado,
            disableText: esPlanillaDescuadrada
              ? 'No es posible guardar los cambios realizados en este ajuste mientras la planilla est descuadrada.'
              : null
          }}
          revisado={{
            permission: 'hallazgo_revisado',
            onClick: this.onRevisado,
            record: item,
            disableText: revisadoDisableText
          }}
        />
      </Fragment>
    )
  }

  render() {
    return (
      <Fragment>
        <div className='content'>
          {this.renderItemHeader()}
          <table className='ui table compact'>
            <thead>
              <tr>
                <th>Cdigo de la Cuenta</th>
                <th>Nombre de la Cuenta</th>
                <th>Debe</th>
                <th>Haber</th>
                <th />
              </tr>
            </thead>
            <tbody>
              {this.renderRows()}
              {this.props.isEditable && (
                <tr>
                  <td colSpan={5}>
                    <button type='button' className='ui button olive fluid icon' onClick={this.onAddItem}>
                      Aadir Fila <i className='icon plus' />
                    </button>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
          {this.esPlanillaDescuadrada() && (
            <div className='ui red tiny message _a-center m1 '>
              <i className='icon exclamation triangle yellow' /> Alerta: Planilla descuadrada
            </div>
          )}
          <div className='ui segment'>
            <EditorPreview
              prefixPreview='Descripcin del Ajuste'
              labelTitle='Descripcin del Ajuste'
              ckeditorOptions='basic'
              editable={this.props.isEditable}
              data={this.state.descripcion}
              onSave={this.onChangeDescripcion}
            />
          </div>

          <div className='ui segment basic nopad pull-left' style={{ marginTop: 5 }}>
            <label style={{ marginRight: 10, display: 'inline-block', fontWeight: 600 }}>Error Tolerable:</label>
            {accounting.formatNumber(app.vars.error_tolerable[this.props.moduleParams.id_visita])}
          </div>

          <div className='ui right aligned segment basic nopad'>
            <label style={{ marginRight: 10, display: 'inline-block', fontWeight: 600 }}>
              Ajuste Registrado? <GenericTooltip text='@tooltip.ajustes.aplicado' /> :
            </label>
            <select
              className='raw-select'
              value={'' + this.state.aplicado == '1' ? '1' : '0'}
              onChange={this.onChangeAplicado}
            >
              <option value='1'>S</option>
              <option value='0'>No</option>
            </select>
          </div>
        </div>
        <div className='actions'>
          {this.renderActions()}
          <div className='clearfix' />
        </div>
      </Fragment>
    )
  }
}

type AjusteTableRowProps = {
  item: ProyectoAjuste
  itemIndex: number
  cuentas: ProyectoAjusteCuenta[]
  isEditable: boolean
  canRemove: boolean
  onUpdate: (item: ProyectoAjuste, itemIndex: number, notify: boolean) => void
  onClickDelete: (item: ProyectoAjuste, itemIndex: number) => void
}

type AjusteTableRowState = {
  cuenta: string
  nombre_cuenta: string
  debe: number
  haber: number
}

class AjusteTableRow extends PureComponent<AjusteTableRowProps, AjusteTableRowState> {
  constructor(props: AjusteTableRowProps) {
    super(props)

    this.state = {
      cuenta: props.item.cuenta,
      nombre_cuenta: props.item.nombre_cuenta,
      debe: props.item.debe,
      haber: props.item.haber
    }
  }

  notifyUpdate(notify: boolean = true) {
    const changes = Object.assign({}, this.props.item, this.state)
    if (changes['cuenta'] == '' || changes['nombre_cuenta'] == '' || (!changes['debe'] && !changes['haber'])) {
      return
    }

    this.props.onUpdate(changes, this.props.itemIndex, notify)
  }

  onClickDelete = (ev: SyntheticEvent) => {
    ev.preventDefault()
    if (!this.props.canRemove) {
      return
    }

    bootbox.confirm(
      'Desea eliminar esta fila de su ajuste contable?',
      'Cancelar',
      'S, eliminar fila',
      (r: boolean) => {
        if (!r) {
          return
        }

        this.props.onClickDelete(this.props.item, this.props.itemIndex)
      }
    )
  }

  onSelectCuenta = (value: string, item: ProyectoAjuste) => {
    this.setState({ cuenta: item.cuenta, nombre_cuenta: item.nombre_cuenta }, () => {
      this.notifyUpdate()
    })
  }

  onChangeCuenta = (ev: SyntheticEvent, value: string) => {
    this.setState({ cuenta: value, nombre_cuenta: '' })
  }

  onChangeNombreCuenta = (ev: SyntheticEvent, value: string) => {
    this.setState({ nombre_cuenta: value, cuenta: '' })
  }

  onBlurCuenta = () => {
    const { cuentas } = this.props

    this.setState(
      (prevState) => {
        if (prevState.cuenta == '' && prevState.nombre_cuenta == '') {
          return null
        }

        if (prevState.cuenta != '' && prevState.nombre_cuenta != '') {
          return null
        }

        if (prevState.cuenta != '') {
          const nombre_cuenta: string = getNombreCuenta(cuentas, prevState.cuenta)

          return { cuenta: prevState.cuenta, nombre_cuenta }
        }

        if (prevState.nombre_cuenta != '') {
          const cuenta = _.find(
            cuentas,
            (item) => item.nombre_cuenta.toUpperCase() == prevState.nombre_cuenta.toUpperCase()
          )

          return { cuenta: cuenta ? cuenta.cuenta : '', nombre_cuenta: prevState.nombre_cuenta }
        }
      },
      () => this.notifyUpdate()
    )
  }

  onChangeInput = (field: 'debe' | 'haber', value: number, persist: boolean) => {
    if (field == 'debe') {
      this.setState({ debe: value }, () => this.notifyUpdate(persist))
    } else {
      this.setState({ haber: value }, () => this.notifyUpdate(persist))
    }
  }

  render() {
    return (
      <tr>
        <td>
          <Autocomplete
            items={this.props.cuentas}
            getItemValue={(item) => item.cuenta}
            renderItem={(item, isHighlighted) => {
              return (
                <div key={item.id} className={'item ' + (isHighlighted ? 'item-highlighted' : '')}>
                  <div>{item.cuenta}</div>
                  <div className='description'>{item.nombre_cuenta}</div>
                </div>
              )
            }}
            value={this.state.cuenta}
            onChange={this.onChangeCuenta}
            onSelect={this.onSelectCuenta}
            inputProps={{ type: 'text', spellCheck: false, required: true, onBlur: this.onBlurCuenta }}
            shouldItemRender={(item, value) => {
              return item.cuenta.replace(/\D/g, '').startsWith(value.replace(/\D/g, ''))
            }}
            renderMenu={(items) => {
              return (
                <div className='autocomplete-menu' style={{ position: 'absolute', zIndex: 1002 }} children={items} />
              )
            }}
            wrapperProps={{ className: cx({ error: !this.state.cuenta }) }}
            wrapperStyle={{ display: 'inline-block', position: 'relative' }}
          />
          {!this.state.cuenta && <div className='ui pointing above prompt mini label'>Requerido</div>}
        </td>
        <td>
          <Autocomplete
            items={this.props.cuentas}
            getItemValue={(item) => item.nombre_cuenta}
            renderItem={(item, isHighlighted) => {
              return (
                <div key={item.id} className={'item ' + (isHighlighted ? 'item-highlighted' : '')}>
                  <div>{item.nombre_cuenta}</div>
                  <div className='description'>{item.cuenta}</div>
                </div>
              )
            }}
            value={this.state.nombre_cuenta}
            onChange={this.onChangeNombreCuenta}
            onSelect={this.onSelectCuenta}
            inputProps={{ type: 'text', spellCheck: false, required: true }}
            shouldItemRender={(item, value) => {
              const text = item.nombre_cuenta.normalize('NFD').replace(/[\u0300-\u036f]/g, '').toUpperCase()
              return _.str.contains(text, value.normalize('NFD').replace(/[\u0300-\u036f]/g, '').toUpperCase())
            }}
            renderMenu={(items) => {
              return (
                <div className='autocomplete-menu' style={{ position: 'absolute', zIndex: 1002 }} children={items} />
              )
            }}
            wrapperProps={{ className: cx({ error: !this.state.nombre_cuenta }) }}
            wrapperStyle={{ display: 'inline-block', position: 'relative' }}
          />
          {!this.state.nombre_cuenta && <div className='ui pointing above prompt mini label'>Requerido</div>}
        </td>
        <td>
          <div className={cx({ error: !this.state.debe && !this.state.haber })}>
            <InputAutoNumeric
              isEditable={this.props.isEditable}
              value={this.state.debe}
              disabled={!!this.state.haber}
              onChange={(ev: SyntheticEvent, value: number) => this.onChangeInput('debe', value, false)}
              onBlur={(ev: SyntheticEvent, value: number) => this.onChangeInput('debe', value, true)}
            />
          </div>
          {!this.state.debe &&
          !this.state.haber && <div className='ui pointing above prompt mini label'>Requerido</div>}
        </td>
        <td>
          <div className={cx({ error: !this.state.haber && !this.state.debe })}>
            <InputAutoNumeric
              isEditable={this.props.isEditable}
              value={this.state.haber}
              disabled={!!this.state.debe}
              onChange={(ev: SyntheticEvent, value: number) => this.onChangeInput('haber', value, false)}
              onBlur={(ev: SyntheticEvent, value: number) => this.onChangeInput('haber', value, true)}
            />
          </div>
          {!this.state.haber &&
          !this.state.debe && <div className='ui pointing above prompt mini label'>Requerido</div>}
        </td>
        <td>
          {this.props.isEditable && (
            <Popup
              disabled={this.props.canRemove}
              content='Todo ajuste contable debe tener un mnimo de 1 cuenta (fila).'
              trigger={
                <a href='#' onClick={this.onClickDelete} title='Eliminar Fila'>
                  <i className='icon cancel red' />
                </a>
              }
              header='Eliminar fila | Opcin deshabilitada'
              className='disable-text'
            />
          )}
        </td>
      </tr>
    )
  }
}

const num = (input: any) => {
  const v = parseFloat(input)
  return isNaN(v) ? 0 : v
}

const createAjusteEmpty = (): ProyectoAjuste => {
  return {
    cuenta: '',
    debe: null,
    haber: null,
    nombre_cuenta: ''
  }
}
