import { PureComponent, SyntheticEvent, Fragment } from 'react'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import { MenuElementListPanel, MenuElementListActions } from './menuitem'
import { AjusteModal } from './ajuste_modal'
import { AuthorIconLogDefaultWrapper } from '../../global/AuthorIconLog'
import { StatusDivBlock } from '../../global/StatusButtonAction'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'
import { truncateText } from '../utils_truncate'

export class AjusteList extends PureComponent<DefaultMenuItemListProps<ProyectoAjusteGroup>> {
  render() {
    if (this.props.items === null) {
      return (
        <div style={{ height: 300, position: 'relative' }}>
          <div className='ui active inverted text loader elastic olive'>Cargando Listado de Ajustes</div>
        </div>
      )
    } else if (_.size(this.props.items) == 0) {
      return <div className='empty nocontent'>0 Ajustes</div>
    } else {
      const { items } = this.props
      return (
        <MenuElementListPanel className='ajustes-list'>
          {_.map(items, (item: ProyectoAjusteGroup) => {
            return (
              <div key={item.idx} className='listitem ajuste-listitem'>
                <AjusteListItem
                  item={item}
                  isEditable={this.props.isEditable}
                  onSave={this.props.onSaveItem}
                  onClickDelete={this.props.onClickDeleteItem}
                />
              </div>
            )
          })}
        </MenuElementListPanel>
      )
    }
  }
}

type AjusteListItemProps = {
  item: ProyectoAjusteGroup
  onSave: (item: ProyectoAjusteGroup, changes: any, blockingText?: string) => void
  onClickDelete: (item: ProyectoAjusteGroup) => void
  isEditable: boolean
}

class AjusteListItem extends PureComponent<AjusteListItemProps, { isModalEdit: boolean }> {
  state = {
    isModalEdit: false
  }

  onClickDelete = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onClickDelete(this.props.item)
  }

  onClickEdit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.setState({ isModalEdit: true })
  }

  onCloseEdit = () => {
    this.setState({ isModalEdit: false })
  }

  onSave = (item: ProyectoAjusteGroup, changes: any, blockingText?: string) => {
    this.props.onSave(item, changes, blockingText)
  }
  render() {
    const { item } = this.props
    return (
      <div>
        {this.props.isEditable && (
          <MenuElementListActions
            actions={{
              edit: { label: 'Editar', callback: this.onClickEdit, permission: 'ajustes_edit' },
              delete: { label: 'Eliminar', callback: this.onClickDelete, permission: 'ajustes_delete' }
            }}
          />
        )}
        <div onClick={this.onClickEdit}>
          <div className='row r1'>
            <div className='pull-left r1_1'>
              <span className='title-h1'> Ajuste #{item.idx}</span>
              <AuthorIconLogDefaultWrapper model={item} size='small' />
              <div className='r1_2'>
                <strong>Ajuste Registrado: </strong> {'' + item.aplicado == '1' ? 'S' : 'No'}
              </div>
            </div>
            <StatusDivBlock
              wrapperClassName='pull-right text-mini'
              status={item.status || 'EN PROCESO'}
              className='mini'
              hideLabel
            />
          </div>
          <table className='ui compact very basic celled striped small table table-ajustes'>
            <thead>
              <tr>
                <th>Cuenta</th>
                <th>Debe</th>
                <th>Haber</th>
              </tr>
            </thead>
            <tbody>
              {_.map(item.items, (subitem) => {
                return (
                  <Fragment key={subitem.id}>
                    {!isEmpty(subitem.debe) && (
                      <tr>
                        <td>
                          <Popup
                            trigger={<span>{subitem.cuenta}</span>}
                            content={subitem.nombre_cuenta}
                            header={subitem.cuenta}
                            size='mini'
                          />
                        </td>
                        <td className='right aligned'>{accounting.formatNumber(subitem.debe)}</td>
                        <td>&nbsp;</td>
                      </tr>
                    )}
                    {!isEmpty(subitem.haber) && (
                      <tr>
                        <td>
                          <Popup
                            trigger={<span>{subitem.cuenta}</span>}
                            content={subitem.nombre_cuenta}
                            header={subitem.cuenta}
                            size='mini'
                          />
                        </td>
                        <td>&nbsp;</td>
                        <td className='right aligned'>{accounting.formatNumber(subitem.haber)}</td>
                      </tr>
                    )}
                  </Fragment>
                )
              })}
            </tbody>
          </table>
          {item.descripcion && (
            <div className='item-description'>
              <div className='editor-preview' dangerouslySetInnerHTML={{ __html: truncateText(item.descripcion) }} />
            </div>
          )}
        </div>
        {this.state.isModalEdit && (
          <AjusteModalEdit
            item={item}
            onSave={this.onSave}
            onClickDelete={this.onClickDelete}
            onClose={this.onCloseEdit}
            isEditable={this.props.isEditable}
          />
        )}
      </div>
    )
  }
}

type AjusteModalEditProps = {
  item: ProyectoAjusteGroup
  onSave: (item: ProyectoAjusteGroup, data: ProyectoAjusteGroup, blockingText?: string) => void
  onClickDelete: (ev: SyntheticEvent) => void
  onClose: () => void
  isEditable: boolean
}

class AjusteModalEdit extends PureComponent<AjusteModalEditProps> {
  onSave = (data: any, blockingText?: string) => {
    this.props.onSave(this.props.item, data, blockingText)
  }

  render() {
    return (
      <Modal
        open
        dimmer='inverted'
        className='modal-ab modal-sidebarmenuitem'
        size='large'
        closeIcon
        onClose={this.props.onClose}
        closeOnEscape={false}
        closeOnDimmerClick={false}
      >
        <Modal.Header>Editar Ajuste</Modal.Header>

        <AjusteModal
          moduleParams={{ id_visita: this.props.item.id_visita, model: 'ajuste' }}
          item={this.props.item}
          isEditable={this.props.isEditable}
          onSave={this.onSave}
          onClickDelete={this.props.onClickDelete}
        />
      </Modal>
    )
  }
}

const isEmpty = (value: any) => {
  if (_.isNumber(value)) {
    return false
  }

  return _.size(value) == 0
}
