import { Component } from 'react'

import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon'
import Loader from 'semantic-ui-react/dist/commonjs/elements/Loader'
import Label from 'semantic-ui-react/dist/commonjs/elements/Label'
import Table from 'semantic-ui-react/dist/commonjs/collections/Table'
import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown'
import Dimmer from 'semantic-ui-react/dist/commonjs/modules/Dimmer'
import Select from 'semantic-ui-react/dist/commonjs/addons/Select'

import EditorPreview from '../global/EditorPreview'
import { AttachmentListPanel } from './Attachments'

export default class TableRowInstruccion extends Component<TableRowInstruccionProps, TableRowInstruccionState> {
  constructor(props: TableRowInstruccionProps) {
    super(props)

    this.state = {
      instruccion: props.instruccion,
      isEditorOpen: false,
      isLoadingAttachments: false
    }
  }

  static getDerivedStateFromProps(nextProps: TableRowInstruccionProps, prevState: TableRowInstruccionState) {
    if (nextProps.instruccion.updated_at != prevState.instruccion.updated_at) {
      return { instruccion: nextProps.instruccion }
    }

    return null
  }

  // Static methods

  evalIsDone = (instruccion: Instruccion) => {
    if (instruccion.answer) {
      return true
    }

    if (instruccion.observations) {
      return true
    }

    return false
  }

  checkIsDoneDisabled = (instruccion: Instruccion) => {
    if (!user_has_role_permission('hoja_trabajo_edit')) {
      return true
    }
    if (!this.props.isEditable) {
      return true
    }
    if (instruccion.answer) {
      return false
    }

    if (instruccion.observations) {
      return false
    }

    return true
  }

  onChecked = (event: React.SyntheticEvent) => {
    const checked = (event.target as any).checked
    this.setState(
      (state: TableRowInstruccionState) => {
        let instruccion = Object.assign({}, state.instruccion)
        instruccion.is_done = checked

        return { instruccion }
      },
      () => this.props.onUpdate(this.state.instruccion, this.props.index)
    )
  }

  onActionChanged = (event: React.SyntheticEvent, data: any) => {
    this.updateModel('answer', data.value)
  }

  onChangeEditor = (field: 'observations' | 'description') => (
    editor: CKEDITOR.editor,
    closeModal: boolean = false,
    autosave: boolean = false,
    cb: () => void
  ) => {
    if (field == 'description') {
      $.blockUI(create_blockUI_loading_message('Guardando los cambios... Por Favor Espere'))
      this.updateModel(field, editor.getData(), () => {
        if ($.isFunction(cb)) {
          cb()
        }
        $.unblockUI()
      })
    } else {
      this.updateModel(field, editor.getData(), cb)
    }
    if (closeModal) {
      this.setState({ isEditorOpen: false })
    }
  }

  onRemove = () => {
    bootbox.warningConfirm('Desea eliminar permanentemente esta instruccion ?', 'Cancelar', 'Aceptar', (r) => {
      if (r) {
        $.blockUI(create_blockUI_loading_message('Eliminando instruccin...'))
        this.props.onRemove(this.props.index)

        if (this.props.instruccion.id) {
          const url = url_for(
            `proyecto/${this.state.instruccion.Visita.HojaTrabajo.proyecto.id}/hoja-trabajo/instruccion/remove`
          )
          $.ajax(url, {
            type: 'POST',
            data: { id: { local_id: this.props.instruccion.local_id } },
            dataType: 'json'
          }).done((res) => {
            if (res.response.record) {
              this.props.onUpdateVisita([ res.response.record ])
            }
            $.unblockUI()
          })
        } else {
          $.unblockUI()
        }
      }
    })
  }

  onUpload = () => {
    this.fetchAttachments()
  }

  onClickAttachmentCell = (ev: React.SyntheticEvent) => {
    ev.preventDefault()
    this.setState({ isEditorOpen: true })
  }

  onUpdateAttachment = () => {
    this.fetchAttachments()
  }

  onDeleteAttachment = (adjunto: ProyectoAttachment, cb: () => void) => {
    this.fetchAttachments()
    this.setState(
      (state) => {
        let instruccion: Instruccion = Object.assign({}, state.instruccion)

        // ------ Try to remove links to file
        const h = public_path(`uploads/proyecto_adjunto/${adjunto.filepath}`, true)
          .replace(/\./g, '.')
          .replace(/\\/g, '/')
        const t = '<a .*href="' + h + '"(.+?)/a>\\s*(<br\\s?/?>)?'
        const re = new RegExp(t, 'g')
        instruccion.observations = instruccion.observations.replace(re, '')

        this.updateModel('observations', instruccion.observations)

        return { instruccion }
      },
      () => {
        if ($.isFunction(cb)) {
          cb()
        }
      }
    )
  }

  // Class methods

  getFormattedIteration() {
    return _.padLeft(String(this.props.iteration + 1), 3, '0')
  }

  getFormattedDescription() {
    let instruccion = this.state.instruccion.description
    if (instruccion == null) {
      return ''
    }

    instruccion = instruccion.replace(/(?!^)\([a-z]\)/gs, '<br/>&nbsp;&nbsp;$& ')

    return (
      '<span key="instruction-iteration" class="instruction-iteration">' +
      this.getFormattedIteration() +
      '.</span> ' +
      instruccion
    )
  }

  getFormattedObservations() {
    let observaciones = this.state.instruccion.observations

    if (!observaciones) {
      return ''
    }

    observaciones = observaciones.trim()

    var cleanHtml = observaciones.replace(/<img.*?>/gs, ' [** IMAGEN  TABLA **] ')

    cleanHtml = cleanHtml.replace(/<table.*?\/table>/gs, '[** TABLA **]')
    cleanHtml = cleanHtml.replace(/<figure.*?\/figure>/gs, '[** IMAGEN **]')
    cleanHtml = cleanHtml.replace(/<\/p>/g, '<br>')
    cleanHtml = cleanHtml.replace(/<\/h\d>/g, '<br>')
    cleanHtml = cleanHtml.replace(/&nbsp;/g, '')

    cleanHtml = cleanHtml.replace(/<(?!(br)\s*\/?)[^>]+>/gs, '')
    cleanHtml = cleanHtml.replace(/<br>\s*<br>/gs, '<br>')

    cleanHtml = cleanHtml.replace(/\[\*\*(.+?)\*\*\]/gs, '<span class="special-tag">$&</span>')

    return cleanHtml
  }

  doUpdateModel(
    field: TableRowInstruccionUpdatableField,
    value: any,
    cb?: (modified_data: TableRowInstruccionServerDataParamData[]) => void
  ) {
    let modified_data: TableRowInstruccionServerDataParamData[] = []
    this.setState(
      (state) => {
        let instruccion: Instruccion = Object.assign({}, state.instruccion)
        if (_.str.trim(value) == '') {
          value = null
        }
        if (field == 'is_done') {
          instruccion[field] = !!value
        } else {
          instruccion[field] = value
        }

        modified_data.push({ field, value })

        if (field != 'description') {
          instruccion.is_done = this.evalIsDone(instruccion)
          modified_data.push({ field: 'is_done', value: instruccion.is_done ? 1 : 0 })
        }

        if (
          field == 'observations' &&
          instruccion.observations != null &&
          instruccion.observations != '' &&
          !instruccion.answer
        ) {
          instruccion.answer = 'Hecho'
          modified_data.push({ field: 'answer', value: 'Hecho' })
        }

        return { instruccion }
      },
      () => {
        this.props.onUpdate(this.state.instruccion, this.props.index)
        if ($.isFunction(cb)) {
          cb(modified_data)
        }
      }
    )
  }

  updateModel(field: TableRowInstruccionUpdatableField, value: any, cb?: () => void) {
    this.doUpdateModel(field, value, (modified_data: TableRowInstruccionServerDataParamData[]) => {
      this.sendInstructionToServer(modified_data, () => {
        this.fetchAttachments(false)
        if ($.isFunction(cb)) {
          cb()
        }
      })
    })
  }

  sendInstructionToServer(data: TableRowInstruccionServerDataParamData[], cb: () => void = null) {
    const params_data: TableRowInstruccionServerDataParams = {
      id: { local_id: this.state.instruccion.local_id },
      id_paso: this.state.instruccion.Visita.HojaTrabajo.paso.id,
      id_visita: this.state.instruccion.Visita.id_visita,
      data
    }
    const url = url_for(`proyecto/${this.state.instruccion.Visita.HojaTrabajo.proyecto.id}/hoja-trabajo/instruccion`)
    if (!this.state.instruccion.id) {
      $.blockUI(create_blockUI_loading_message('Creando instruccin...'))
    }
    $.ajax(url, { type: 'POST', data: params_data, dataType: 'json' }).done((res) => {
      if (res.response.new) {
        this.setState({ instruccion: Object.assign({}, this.state.instruccion, res.response.record) })
        $.unblockUI()
      }

      this.props.onUpdateVisita(res.response.visitas, res.response.changed)
      if ($.isFunction(cb)) {
        cb()
      }
    })
  }

  fetchAttachments(showLoading: boolean = true) {
    if (showLoading) {
      this.setState({ isLoadingAttachments: true })
    }

    const url = url_for(
      `proyecto/${this.state.instruccion.Visita.HojaTrabajo.proyecto.id}/hoja-trabajo/instruccion/attachments`
    )
    $.ajax(url, { type: 'GET', data: { local_id: this.state.instruccion.local_id }, dataType: 'json' }).done((res) => {
      this.doUpdateModel('Attachments', res.response.Attachments)
      this.setState({ isLoadingAttachments: false })
    })
  }

  render() {
    const { instruccion } = this.state

    let actionChoices: any[] = [ 'Hecho', 'S', 'No', 'Cumple', 'No Cumple', 'Aplica', 'No Aplica' ]
    actionChoices = actionChoices.map((_item) => ({ key: _item, value: _item, text: _item }))

    const formattedDescription = this.getFormattedDescription()
    const ckeditorOptionsInstr = {
      uploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&model=instruccion_item__instr&model_id=' +
          instruccion.local_id
      ),
      imageUploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&model=instruccion_item__instr&model_id=' +
          instruccion.local_id
      ),
      filebrowserUploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&n=attachment&model=instruccion_item__instr&model_id=' +
          instruccion.local_id
      ),
      link_to_muestreo_enabled: true
    }

    const ckeditorOptionsObs = {
      uploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&model=instruccion_item__obs&model_id=' +
          instruccion.local_id
      ),
      imageUploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&model=instruccion_item__obs&model_id=' +
          instruccion.local_id
      ),
      filebrowserUploadUrl: url_for(
        'proyecto/' +
          instruccion.Visita.HojaTrabajo.proyecto.id +
          '/master_data/paso/' +
          instruccion.Visita.HojaTrabajo.paso.id +
          '/upload?id_visita=' +
          instruccion.Visita.id_visita +
          '&n=attachment&model=instruccion_item__obs&model_id=' +
          instruccion.local_id
      ),
      link_to_muestreo_enabled: true
    }

    const attachmentsList = (
      <div style={{ position: 'relative' }}>
        <Dimmer inverted active={this.state.isLoadingAttachments}>
          <Loader inverted />
        </Dimmer>
        <AttachmentListPanel
          attachments={instruccion.Attachments}
          onUpdateItem={this.onUpdateAttachment}
          onDeleteItem={this.onDeleteAttachment}
        />
      </div>
    )

    let modalLeftActions = null
    if (this.props.isEditable && user_has_role_permission('instruccion_delete')) {
      modalLeftActions = (
        <Button
          negative
          floated='left'
          icon='trash alternate outline'
          content='Eliminar instruccin'
          onClick={this.onRemove}
        />
      )
    }

    return (
      <Table.Row
        className={`hoverable-actions instruccion-row ${this.props.iteration % 2 ? 'even' : 'odd'}`}
        id={`table-instruccion-${instruccion.local_id}`}
      >
        <Table.Cell className='td-chk' collapsing>
          <label className='container-checkbox'>
            <input
              id={`checkbox-${instruccion.id}`}
              type='checkbox'
              checked={instruccion.is_done}
              disabled={this.checkIsDoneDisabled(instruccion)}
              value='1'
              onChange={this.onChecked}
            />
            <span className='checkmark' />
          </label>
          {this.props.isEditable &&
          this.props.isSortable &&
          user_has_role_permission('hoja_trabajo_edit') && (
            <div className='action-list'>
              <div className='drag-icon sort-handler' />
            </div>
          )}
        </Table.Cell>
        <Table.Cell width='6' className='instruction-text'>
          <EditorPreview
            className='instruction-text-editor'
            ckeditorOptions={ckeditorOptionsInstr}
            preview={formattedDescription}
            data={instruccion.description}
            required
            label=' '
            labelTitle='Descripcin de la instruccin'
            open={!instruccion.id && !instruccion.description}
            editable={this.props.isEditable && user_has_role_permission('instruccion_edit')}
            leftActions={modalLeftActions}
            autosave
            onSave={this.onChangeEditor('description')}
          />
        </Table.Cell>
        <Table.Cell style={{ width: 'auto' }}>
          {this.props.isEditable && user_has_role_permission('hoja_trabajo_edit') ? (
            <Select
              placeholder='Seleccione...'
              className='instruccion-action'
              value={instruccion.answer}
              clearable
              onChange={this.onActionChanged}
              options={actionChoices}
            />
          ) : (
            <span>{instruccion.answer}</span>
          )}
        </Table.Cell>
        <Table.Cell width='7' className='observations-text'>
          <EditorPreview
            className='observations-text-editor'
            ckeditorOptions={ckeditorOptionsObs}
            preview={this.getFormattedObservations()}
            data={instruccion.observations}
            label={formattedDescription}
            open={this.state.isEditorOpen}
            editable={this.props.isEditable && user_has_role_permission('hoja_trabajo_edit')}
            autosave
            onSave={this.onChangeEditor('observations')}
            onClose={() => this.setState({ isEditorOpen: false })}
            onUpload={this.onUpload}
            onClickPreview={this.onClickAttachmentCell}
            footer={attachmentsList}
          />
        </Table.Cell>
        <Table.Cell className='attachments'>
          <div>
            <a href='#' className='btn-attach' onClick={this.onClickAttachmentCell}>
              <Label circular floating color='green' size='mini'>
                {_.size(instruccion.Attachments)}
              </Label>
              <Icon name='copy outline' className='attachments-icon' />
            </a>
          </div>
        </Table.Cell>
        <Table.Cell textAlign='right' collapsing className='computer'>
          {this.props.isEditable &&
          user_has_role_permission('instruccion_delete') && (
            <Dropdown
              className='action'
              inline
              direction='left'
              icon={false}
              trigger={<Icon name='ellipsis vertical' color='grey' />}
            >
              <Dropdown.Menu>
                <Dropdown.Item icon='trash alternate outline' content='Eliminar instruccin' onClick={this.onRemove} />
              </Dropdown.Menu>
            </Dropdown>
          )}
        </Table.Cell>
      </Table.Row>
    )
  }
}
