import { PureComponent, SyntheticEvent } from 'react'

import { BaseMenuElement } from './menuitem'
import RevisionList from './revision_list'

import Textarea from 'react-textarea-autosize'

type MenuItemRevisionState = {
  items: ProyectoRevisionTree[]
}

export class MenuItemRevision extends PureComponent<
  DefaultMenuElementProps<ProyectoRevisionTree>,
  MenuItemRevisionState
> {
  state: MenuItemRevisionState = {
    items: null
  }

  parseItems = (input: any[]): ProyectoRevisionTree[] => {
    return input
  }

  onMountPanel = () => {
    if (this.state.items === null) {
      this.props.modelManager.getJson(this.props).then((items) => {
        this.setState({ items })
      })
    }
  }

  onCreateItem = (params: { text: string; status: 'NUEVO' | 'COMPLETADO' }, cb: () => void) => {
    $.blockUI(create_blockUI_loading_message('Creando Nota de Revisin'))

    this.props.modelManager
      .createItem(this.props, { revision_text: params.text, op: params.status })
      .then((revision) => {
        this.setState((prevState) => {
          const items = [ ...prevState.items ]
          items.push(revision)

          return { items }
        })

        cb()
        $.unblockUI()
      })
  }

  onSaveItem = (item: ProyectoRevisionTree, changes: any, blockingText?: string) => {
    return new Promise<ProyectoRevisionTree>((resolve) => {
      if (blockingText) {
        setTimeout(() => $.blockUI(create_blockUI_loading_message(blockingText)), 1000 * 0.1)
      }

      this.props.modelManager.saveItem(this.props, item, changes).then((revision) => {
        this.setState((prevState) => {
          const items = _.map(prevState.items, (item) => {
            return item.id == revision.id ? revision : item
          })

          return { items }
        })

        if (blockingText) {
          $.unblockUI()
        }

        resolve(revision)
      })
    })
  }

  onClickDeleteItem = (item: ProyectoRevisionTree) => {
    bootbox.warningConfirm(
      `Desea eliminar esta Nota de Revisin <b>#${item.name}</b> ?`,
      'No',
      'S, Eliminar',
      (r) => {
        if (!r) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Nota de Revisin'))

        this.props.modelManager.deleteItem(this.props, item).then(() => {
          this.setState((prevState) => {
            const items = _.reject(prevState.items, { id: item.id })

            return { items }
          })
          $.unblockUI()
        })
      }
    )
  }

  renderContent() {
    return (
      <div className='content'>
        <RevisionNewForm onSubmit={this.onCreateItem} />
        <RevisionList
          items={this.state.items}
          moduleParams={this.props.moduleParams}
          isEditable={es_proyecto_editable() && user_has_role_permission('revision_edit')}
          onSaveItem={this.onSaveItem}
          onClickDeleteItem={this.onClickDeleteItem}
        />
      </div>
    )
  }

  render() {
    return (
      <BaseMenuElement
        name='revision'
        counter={
          this.state.items === null ? this.props.counter : _.size(_.reject(this.state.items, { status: 'REVISADO' }))
        }
        icon='notas_de_revision'
        title='Notas de Revisin'
        open={this.props.open}
        onToggle={this.props.onOpen}
        onMountPanel={this.onMountPanel}
      >
        {this.renderContent()}
      </BaseMenuElement>
    )
  }
}

type RevisionNewFormProps = {
  onSubmit: (params: { text: string; status: 'NUEVO' | 'COMPLETADO' }, cb: () => void) => void
}

class RevisionNewForm extends PureComponent<RevisionNewFormProps, { text: string; isEditMode: boolean }> {
  state = {
    text: '',
    isEditMode: false
  }

  onChange = (ev: SyntheticEvent) => {
    this.setState({ text: (ev.target as any).value })
  }

  onTextareaKeyDown = (ev: React.KeyboardEvent) => {
    if (ev.keyCode == 27) {
      // on press Esc
      this.onCancel()
    }

    if ((ev.ctrlKey || ev.metaKey) && (ev.keyCode == 13 || ev.keyCode == 10)) {
      // on press ctrl + Enter
      this.onSubmit('NUEVO')
    }
  }

  onClickNew = () => {
    this.setState({ isEditMode: true })
  }

  onCancel = () => {
    this.setState({ isEditMode: false, text: '' })
  }

  onSubmit = (status: 'NUEVO' | 'COMPLETADO') => {
    if (_.str.trim(this.state.text) == '') {
      bootbox.alertError('La Nota de Revisin no puede estar vaca.')
      return
    }

    this.props.onSubmit({ text: _.str.trim(this.state.text), status }, () => {
      this.setState({ text: '', isEditMode: false })
    })
  }

  render() {
    if (!es_proyecto_editable() || !user_has_role_permission('revision_edit')) {
      return null
    }

    return !this.state.isEditMode ? (
      <div style={{ textAlign: 'center' }}>
        <button className='ui button primary mini compact' onClick={this.onClickNew}>
          Crear Nota
        </button>
      </div>
    ) : (
      <div>
        <Textarea
          minRows={2}
          maxRows={8}
          autoFocus
          onChange={this.onChange}
          value={this.state.text}
          style={{ width: '98%' }}
          onKeyDown={this.onTextareaKeyDown}
          placeholder='Escriba aqu su Nota de Revisin'
        />
        <div style={{ marginTop: 8, textAlign: 'right' }}>
          <button className='ui button negative mini compact' onClick={this.onCancel}>
            Cancelar
          </button>
          <button className='ui button primary mini compact' onClick={() => this.onSubmit('NUEVO')}>
            Guardar
          </button>
        </div>
      </div>
    )
  }
}
