import { Component } from 'react'
import TabPaneVisita from './TabPaneVisita'

import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon'
import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Divider from 'semantic-ui-react/dist/commonjs/elements/Divider'
import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu'
import Form from 'semantic-ui-react/dist/commonjs/collections/Form'
import Tab from 'semantic-ui-react/dist/commonjs/modules/Tab'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal'
import Accordion from 'semantic-ui-react/dist/commonjs/modules/Accordion/Accordion'

import { AppVisitaDefaultTab } from './AppVisitaTab'
import _ from 'lodash'

import DualListBox from 'react-dual-listbox'
import HojaTrabajoContext from './HojaTrabajoContext'

declare var app: any

export default class App extends Component<HojaTrabajoAppProps, HojaTrabajoAppState> {
  private panes: HojaTrabajoAppPane[]
  constructor(props: HojaTrabajoAppProps) {
    super(props)

    const locationHash = window.location.hash.replace(/^#!vis-/, '')

    let activeIndex: number
    let visita_current
    if (locationHash) {
      activeIndex = +locationHash
      visita_current = _.find(this.props.hoja_trabajo.visitas, { index: activeIndex })
    } else {
      visita_current = _.find(this.props.hoja_trabajo.visitas, { is_current: true })
      activeIndex = visita_current ? visita_current.index : null
    }

    if (visita_current) {
      app.vars.current_idvisita = visita_current.id_visita
    }

    this.state = {
      activeIndex,
      selectedVisitas: _.filter(_.map(this.props.hoja_trabajo.visitas, (item, id) => (item.id ? id : null))),
      newVisitaOpen: false,
      assocModalOpen: false,
      hoja_trabajo: this.props.hoja_trabajo
    }
  }

  componentDidMount() {
    /**
     * Ancho de la tab.
     */
    const tabWidth: number = 90 / _.size(_.filter(this.panes, { type: 'sheet' }))
    $('#visita-tabs').find('.menu>a.visita-tab').css({ width: tabWidth + '%' })
  }

  handleClick = (e: React.SyntheticEvent, { index }: { index: number }) => {
    this.setState({ activeIndex: index })
    const visita = _.find(this.state.hoja_trabajo.visitas, { index })
    app.vars.current_idvisita = visita.id_visita
    $(`#table-visita-instrucciones-${visita.id}`).floatThead('reflow')
    window.location.hash = '#!vis-' + index
  }

  showModal = () => {
    this.setState({ assocModalOpen: true })
  }

  onChangeVisita = (visita: ProyectoPasoVisita) => {
    this.setState((state: HojaTrabajoAppState) => {
      let hoja_trabajo = Object.assign({}, state.hoja_trabajo)
      hoja_trabajo.visitas = _.map(hoja_trabajo.visitas, (item) => {
        if (item.index + '' == visita.index + '') {
          return visita
        } else {
          return item
        }
      })
      return { hoja_trabajo }
    })
  }

  onChangeApp = (data: any) => {
    this.setState((state: HojaTrabajoAppState) => {
      let hoja_trabajo = Object.assign({}, state.hoja_trabajo, data)

      return { hoja_trabajo }
    })
  }

  getUrlFor(urlToAppend: string) {
    return url_for(
      `proyecto/${this.props.hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${this.props.hoja_trabajo.paso
        .id}/${urlToAppend}`
    )
  }

  isAllowedAsociarVisita() {
    if (!user_has_role_permission('paso_asociar_visita')) {
      return false
    }

    return true
  }

  getAssocModal() {
    if (!this.isAllowedAsociarVisita()) {
      return null
    }

    if (!this.state.hoja_trabajo.proyecto.isOpen) {
      return null
    }

    const { assocModalOpen, selectedVisitas, newVisitaOpen } = this.state

    const icons = {
      moveLeft: <i className='icon angle left' />,
      moveAllLeft: <i className='icon angle double left' />,
      moveRight: <i className='icon angle right' />,
      moveAllRight: <i className='icon angle double right' />,
      moveDown: <i className='icon angle down' />,
      moveUp: <i className='icon angle up' />
    }

    const visitas = this.props.hoja_trabajo.visitas
    let options: { value: number; label: string }[] = []

    _.forEach(visitas, function(visita, id) {
      options.push({
        value: id,
        label: visita.label
      })
    })

    const onChange = (selectedVisitas: number[]) => {
      this.setState({ selectedVisitas })
    }

    const onClose = () => {
      this.setState({ assocModalOpen: false })
    }

    const onToggle = () => {
      this.setState((state) => {
        return { newVisitaOpen: !state.newVisitaOpen }
      })
    }

    return (
      <Modal open={assocModalOpen} closeIcon dimmer='inverted' size='small' onClose={onClose} className='modal-ab'>
        <Modal.Header> Asociacin de Visitas a este Paso</Modal.Header>
        <Modal.Content>
          <form method='post' className='ui form' action={this.getUrlFor('visita')}>
            <div style={{ marginBottom: '15px' }}>Asocie las visitas a relacionar con este paso: </div> <br />
            <DualListBox
              name='visitas'
              options={options}
              availableLabel='Por asociar'
              selectedLabel='Asociadas'
              selected={selectedVisitas}
              icons={icons}
              onChange={onChange}
            />
            <Divider horizontal>O</Divider>
            <Accordion>
              <Accordion.Title active={newVisitaOpen} onClick={onToggle}>
                <Icon name='caret right' /> Crear Nueva Visita
              </Accordion.Title>
              <Accordion.Content active={newVisitaOpen}>
                <Form.Field inline>
                  <label>Nombre de la visita</label>
                  <div className='ui input'>
                    {' '}
                    <input placeholder='Nombre de la visita' name='visita_name' type='text' />{' '}
                  </div>
                </Form.Field>
              </Accordion.Content>
            </Accordion>
            <button className='primary small ui button right floated'>Guardar</button>
            <button className='negative small ui button right floated' type='button' onClick={onClose}>
              Cancelar
            </button>
            <div className='clearfix' />
          </form>
        </Modal.Content>
      </Modal>
    )
  }

  render() {
    const self = this

    const { activeIndex } = this.state

    const assocModal = this.getAssocModal()

    this.panes = []

    let visitas: ProyectoPasoVisita[] = _.values(self.state.hoja_trabajo.visitas)
    visitas = _.sortBy(visitas, 'position')

    _.forEach(visitas, function(visita, k) {
      if (visita.id == null) {
        return
      }

      visita.HojaTrabajo = self.state.hoja_trabajo

      self.panes.push({
        type: 'sheet',
        menuItem: () => {
          return (
            <Menu.Item
              key={visita.id}
              index={visita.index}
              className='tab-nav-item visita-tab'
              name={visita.label}
              onClick={self.handleClick}
              active={activeIndex == visita.index}
            >
              <AppVisitaDefaultTab visita={visita} />
            </Menu.Item>
          )
        },
        pane: () => (
          <TabPaneVisita
            key={visita.id}
            idx={visita.index}
            active={activeIndex == visita.index}
            visita={visita}
            isEditable={self.state.hoja_trabajo.proyecto.isOpen}
            onChange={self.onChangeVisita}
            onChangeApp={self.onChangeApp}
          />
        )
      })
    })

    if (self.panes.length == 0) {
      return (
        <div className='alert alert-block nocontent'>
          <h3>Sin visitas</h3>
          <p>
            <span className='icon-warning-sign' />Este paso aun no tiene visitas asociadas.{' '}
          </p>
          {self.state.hoja_trabajo.proyecto.isOpen &&
          this.isAllowedAsociarVisita() && [
            <Button
              key='button'
              content='Asociar visitas'
              color='green'
              compact
              icon='info circle'
              onClick={this.showModal}
              style={{ marginTop: '30px' }}
            />,
            <div key='modal'>{assocModal}</div>
          ]}
        </div>
      )
    }

    if (this.isAllowedAsociarVisita() && self.state.hoja_trabajo.proyecto.isOpen) {
      self.panes.push({
        type: 'action',
        menuItem: () => {
          return (
            <Menu.Item
              key='plus'
              className='tab-nav-item tab-add'
              title='Asociar/Desasociar visitas a este paso'
              onClick={this.showModal}
            >
              <Icon name='plus' />
            </Menu.Item>
          )
        }
      })
    }

    return (
      <HojaTrabajoContext.Provider value={{ hoja_trabajo: this.state.hoja_trabajo, update: this.onChangeApp }}>
        <div id='visita-tabs' className={`paso-code-${this.state.hoja_trabajo.paso.code}  paso-default`}>
          <Tab panes={this.panes} activeIndex={activeIndex} renderActiveOnly={false} />

          {assocModal}
        </div>
      </HojaTrabajoContext.Provider>
    )
  }
}
