import { PureComponent } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import { AuthorIconLogDefaultWrapper } from '../../global/AuthorIconLog'

type MenuItemNewProps = {
  isEditable: boolean
  headerText: string
  buttonText: string
  onSave: (data: any) => Promise<null>
  moduleParams: any
  item: any
  contentAs: React.ComponentType<MenuItemModalType>
}

type MenuItemModalType = {
  item: any
  isEditable: boolean
  moduleParams: any
  onSave: (item: any) => void
}

export class MenuItemNew extends PureComponent<MenuItemNewProps, { modalOpen: boolean }> {
  state = {
    modalOpen: false
  }

  onClick = () => {
    this.setState({ modalOpen: true })
  }

  onCloseModal = () => {
    this.setState({ modalOpen: false })
  }

  onSave = (data: ProyectoHallazgo) => {
    this.props.onSave(data).then(() => {
      this.onCloseModal()
    })
  }

  render() {
    if (!this.props.isEditable) {
      return
    }

    const MenuItemModal = this.props.contentAs

    return (
      <div className='_a-center'>
        <button className='button ui right tiny compact plain green rounded' onClick={this.onClick}>
          {this.props.buttonText} <i className='icon plus' />
        </button>
        {this.state.modalOpen && (
          <Modal
            open
            dimmer='inverted'
            className='modal-ab modal-sidebarmenuitem'
            size='large'
            closeIcon
            closeOnDimmerClick={false}
            closeOnEscape={false}
            onClose={this.onCloseModal}
          >
            <Modal.Header>{this.props.headerText}</Modal.Header>
            <MenuItemModal
              onSave={this.onSave}
              moduleParams={this.props.moduleParams}
              isEditable={this.props.isEditable}
              item={this.props.item}
            />
          </Modal>
        )}
      </div>
    )
  }
}

type MenuItemNewHeaderProps = {
  item: {
    label: string
    status: ProyectoModuleStatusEnum
  } & IAuthorLog
}

export const MenuItemNewHeader = (props: MenuItemNewHeaderProps) => {
  const status = props.item.status || 'EN PROCESO'
  return (
    <div className='ui grid padded' style={{ marginBottom: 0 }}>
      <div className='row' style={{ paddingTop: 0 }}>
        <div className='column four wide'>
          <strong style={{ fontWeight: 600 }}>{props.item.label}</strong>
        </div>
        <div className='column four wide'>
          <span>Estado: </span>
          <div className='lb-status-wrapper'>
            <div className={`lb-status lb-status-${_.str.dasherize(status.toLowerCase())}`}>{status}</div>
          </div>
        </div>

        <div className='column four wide'>
          <AuthorIconLogDefaultWrapper model={props.item} />
        </div>
      </div>
    </div>
  )
}
