import { render } from 'react-dom'
import App from './App'
import AppPeriodo from './AppPeriodo'

declare var app: any

accounting.settings.number.precision = 0
accounting.settings.number.thousand = '.'
accounting.settings.number.decimal = ','

if (app.vars.hoja_trabajo.paso.tipo == 'PERIODO') {
  render(<AppPeriodo hoja_trabajo={app.vars.hoja_trabajo} />, document.getElementById('root'))
} else {
  render(<App hoja_trabajo={app.vars.hoja_trabajo} />, document.getElementById('root'))
}
