import { Component, Fragment } from 'react'

import Label from 'semantic-ui-react/dist/commonjs/elements/Label/Label'
import Menu from 'semantic-ui-react/dist/commonjs/collections/Menu/Menu'
import Image from 'semantic-ui-react/dist/commonjs/elements/Image/Image'
import { MenuItemProps } from 'semantic-ui-react/dist/commonjs/collections/Menu/MenuItem'
import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon/Icon'

import { HojaTrabajoHistoryModal, HojaTrabajoAsignarVisitaModal } from './TabPaneVisitaModals'
import HojaTrabajoContext from './HojaTrabajoContext'
import { HojaTrabajoUpdateUtils } from './utils'
import { MenuItemAjuste } from './sidebar/menuitem_ajuste'
import { MenuItemHallazgo } from './sidebar/menuitem_hallazgo'
import { MenuItemRevision } from './sidebar/menuitem_revision'

import { HallazgoHojaTrabajoModelManager, RevisionHojaTrabajoModelManager } from './sidebar/menuitem__model_manager'

import cx from 'classnames'

type MenuSidebarVisitaProps = {
  visita: ProyectoPasoVisita
  actionItems?: { [s: string]: boolean | MenuItemProps }
}

type MenuSidebarVisitaState = {
  panelOpen: string
  openAsignarUsuarioModal: boolean
}

export default class MenuSidebarVisita extends Component<MenuSidebarVisitaProps, MenuSidebarVisitaState> {
  static contextType = HojaTrabajoContext
  static defaultProps = {
    actionItems: {}
  }

  context: HojaTrabajoContextType

  state: MenuSidebarVisitaState = {
    panelOpen: null,
    openAsignarUsuarioModal: false
  }

  isActionItemEnabled(action: string) {
    return typeof this.props.actionItems[`_${action}`] === 'undefined' || this.props.actionItems[`_${action}`]
  }

  onItemOpen = (item: string) => {
    this.setState({ panelOpen: item })
  }

  closeAllPanel = () => {
    this.setState({ panelOpen: null })
  }

  renderAsignUserModal() {
    if (this.state.openAsignarUsuarioModal) {
      const { hoja_trabajo, update } = this.context
      const onSaveAsignarVisita = (id_asignado: string) => {
        const url = url_for(
          `proyecto/${hoja_trabajo.proyecto.id}/hoja-trabajo/paso/${hoja_trabajo.paso.id}/visita/${this.props.visita
            .id_visita}/save`
        )
        $.blockUI(create_blockUI_loading_message('Guardando asignado...'))

        $.ajax(url, { type: 'POST', data: { op: 'a', paso_id_usuario: id_asignado }, dataType: 'json' }).done((res) => {
          let hoja_trabajo_copy = HojaTrabajoUpdateUtils.updateVisita(
            hoja_trabajo,
            this.props.visita,
            res.response.record
          )
          update(hoja_trabajo_copy)
          $.unblockUI()
        })

        this.setState({ openAsignarUsuarioModal: false })
      }

      return (
        <HojaTrabajoAsignarVisitaModal
          visita={this.props.visita}
          onSave={onSaveAsignarVisita}
          onClose={() => this.setState({ openAsignarUsuarioModal: false })}
        />
      )
    }
  }

  render() {
    const id_proyecto = this.props.visita.HojaTrabajo.proyecto.id
    const id_paso = this.props.visita.HojaTrabajo.paso.id
    const id_visita = this.props.visita.id_visita
    const has_parent = this.props.visita.HojaTrabajo.proyecto.id_parent != null

    const url_base = `proyecto/${id_proyecto}/paso/${id_paso}/visita/${id_visita}/`

    const counters = this.props.visita.additionals

    const image_path = public_path('images/sidebar_right/')

    return (
      <Fragment>
        <div
          className={cx('sidebar-fixed-overlay ui page dimmer inverted', { active: this.state.panelOpen != null })}
          onClick={() => this.closeAllPanel()}
        />
        <Menu fixed='right' vertical compact icon className='visita-sidebar right-sidebar' id='app-right-sidebar'>
          <Menu.Menu>
            {_.map(this.props.actionItems, (actionItem: MenuItemProps, key: string) => {
              if (key.substr(0, 1) == '_' || actionItem === null) {
                return null
              }

              const _onClick = actionItem.onClick
              actionItem.onClick = (ev: any, data: any) => {
                this.closeAllPanel()
                _onClick(ev, data)
              }
              return <Menu.Item key={key} {...actionItem} />
            })}

            {this.props.visita.HojaTrabajo.proyecto.isOpen &&
            user_has_role_permission('paso_asignar_usuario') &&
            this.isActionItemEnabled('assign_user') && (
              <Menu.Item
                link
                title='Asignar usuario a esta visita'
                onClick={(ev) => {
                  this.closeAllPanel()
                  this.setState({ openAsignarUsuarioModal: true })
                }}
              >
                <Icon.Group size='large'>
                  <Icon name='user outline' />
                  <Icon corner name='plus' />
                </Icon.Group>
              </Menu.Item>
            )}
            {this.renderAsignUserModal()}

            {has_parent &&
            this.isActionItemEnabled('history') && (
              <HojaTrabajoHistoryModal
                trigger={
                  <Menu.Item
                    title='Hoja Trabajo Ao Anterior '
                    icon={{ name: 'history', size: 'large' }}
                    onClick={this.closeAllPanel}
                  />
                }
                url={url_for(url_base + 'prev')}
              />
            )}
          </Menu.Menu>

          <Menu.Menu>
            {this.isActionItemEnabled('ajustes') && (
              <MenuItemAjuste
                counter={counters.ajustes}
                moduleParams={{ id_paso, id_visita, model: 'ajuste' }}
                open={this.state.panelOpen == 'ajuste'}
                onOpen={this.onItemOpen}
                modelManager={null}
              />
            )}
            {this.isActionItemEnabled('hallazgo') && (
              <MenuItemHallazgo
                modelManager={HallazgoHojaTrabajoModelManager.getInstance()}
                counter={counters.hallazgos}
                moduleParams={{ id_paso, id_visita, model: 'hallazgo' }}
                open={this.state.panelOpen == 'hallazgo'}
                onOpen={this.onItemOpen}
              />
            )}
            {this.isActionItemEnabled('muestreo') && (
              <Menu.Item
                as='a'
                className='badged'
                href={url_for(url_base + 'muestreo')}
                title='Muestreo'
                onClick={this.closeAllPanel}
              >
                <Image src={image_path + 'muestreo.svg'} size='large' /> {' '}
                <Label
                  size='mini'
                  className='right'
                  floating
                  circular
                  content={counters.muestreos}
                  color={counters.muestreos == 0 ? 'green' : 'red'}
                />
              </Menu.Item>
            )}
            {this.isActionItemEnabled('revision') && (
              <MenuItemRevision
                counter={counters.revisiones}
                moduleParams={{ id_paso, id_visita, model: 'revision' }}
                modelManager={RevisionHojaTrabajoModelManager.getInstance(this.context)}
                open={this.state.panelOpen == 'revision'}
                onOpen={this.onItemOpen}
              />
            )}
          </Menu.Menu>
          {this.isActionItemEnabled('pdf') && (
            <Menu.Menu>
              <Menu.Item
                as='a'
                href={url_for(url_base + 'pdf.pdf')}
                target='_blank'
                title='Exportar como PDF'
                content={<Image src={image_path + 'exportar_pdf.svg'} size='large' />}
              />
            </Menu.Menu>
          )}
        </Menu>
      </Fragment>
    )
  }
}
