import React, { PureComponent, Fragment, SyntheticEvent } from 'react'

import EditorPreview from '../../global/EditorPreview'
import { StatusButtonGroupActions } from '../../global/StatusButtonAction'
import DatePicker, { registerLocale } from 'react-datepicker'
import datepickerEs from 'date-fns/locale/es'
import { format as formatDate, parseISO as parseDate, isValid as isValidDate } from 'date-fns'
import { MentionsInput, Mention } from 'react-mentions'
import { HallazgoUtils } from '../utils_hallazgo'
import cx from 'classnames'
import { MenuItemNewHeader } from './menuitem__new'

declare var app: any

registerLocale('es', datepickerEs)

type HallazgoModalProps = {
  moduleParams: any
  item: ProyectoHallazgo
  isEditable: boolean
  onSave: (item: Partial<ProyectoHallazgo>) => void
  onClickDelete?: (ev: SyntheticEvent) => void
}

type HallazgoModalState = {
  value: Partial<ProyectoHallazgo>
}

export class HallazgoModal extends PureComponent<HallazgoModalProps, HallazgoModalState> {
  constructor(props: HallazgoModalProps) {
    super(props)

    this.state = {
      value: props.item
    }
  }

  private handlers: { [s: string]: any } = {}

  attachHandler(field: string, handler: any) {
    if (!this.handlers[field]) {
      this.handlers[field] = handler
    }

    return this.handlers[field]
  }

  isNewItem() {
    return this.props.item.id == null
  }

  updateModel(changes: any) {
    this.setState((prevState) => {
      return { value: Object.assign({}, prevState.value, changes) }
    })

    if (!this.isNewItem()) {
      this.props.onSave(changes)
    }
  }

  getDate(date: string): Date {
    if (!date) {
      return null
    }

    const d = parseDate(date)

    return isValidDate(d) ? d : null
  }

  formatImplementadoChoice(implementado: string | boolean) {
    if (implementado === null || implementado === '') {
      return ''
    }

    return implementado === true || implementado === '1' ? '1' : '0'
  }

  _onCreate(changes: any) {
    if (_.isEmpty(this.state.value.hallazgo)) {
      bootbox.alert('La descripcin del hallazgo NO puede ser un campo vaco.')
      return
    }

    this.props.onSave(Object.assign({}, this.state.value, changes))
  }

  onCreate = () => {
    this._onCreate({ status: 'EN PROCESO' })
  }

  onCreateAndCompletado = () => {
    this._onCreate({ status: 'COMPLETADO' })
  }

  onCompletado = () => {
    this.props.onSave({ status: 'COMPLETADO' })
  }

  onRevisado = () => {
    this.props.onSave({ status: 'REVISADO' })
  }

  onEditorChange = (field: keyof ProyectoHallazgo) => {
    const handler = (editor: CKEDITOR.editor, closeModal: boolean, autosave: boolean, cb: () => void) => {
      const value = editor.getData()
      if (field == 'hallazgo' && !this.isNewItem() && _.size(_.str.trim(value)) == 0) {
        bootbox.alert('La descripcin del hallazgo NO puede ser un campo vaco.')
        return
      }

      this.updateModel({ [field]: value })
      if ($.isFunction(cb)) {
        cb()
      }
    }

    return this.attachHandler(field, handler)
  }

  onInputChange = (field: keyof ProyectoHallazgo) => {
    return this.attachHandler(field, (ev: SyntheticEvent) => {
      this.updateModel({ [field]: (ev.target as any).value })
    })
  }

  onDateChange = (field: keyof ProyectoHallazgo) => {
    return this.attachHandler(field, (value: Date) => {
      this.updateModel({ [field]: formatDate(value, 'yyyy-MM-dd') })
    })
  }
  onValueChange = (field: keyof ProyectoHallazgo) => {
    return this.attachHandler(field, (value: string) => {
      this.updateModel({ [field]: value })
    })
  }

  onClickDelete = (ev: SyntheticEvent) => {
    if ($.isFunction(this.props.onClickDelete)) {
      this.props.onClickDelete(ev)
    }
  }

  renderActions() {
    const { item } = this.props

    const rightActions = item.id !== null && (
      <button className='ui button compact negative left floated' onClick={this.onClickDelete}>
        <i className='icon trash' /> Eliminar
      </button>
    )

    return item.id === null ? (
      <div className='actions ab-status-actions'>
        <button className='ui button compact green' onClick={this.onCreate}>
          Crear
        </button>

        <button className='ui button compact green' onClick={this.onCreateAndCompletado}>
          Crear y Completar
        </button>
      </div>
    ) : (
      <Fragment>
        {rightActions}
        <StatusButtonGroupActions
          completado={{ permission: 'hallazgo_completado', onClick: this.onCompletado }}
          revisado={{ permission: 'hallazgo_revisado', onClick: this.onRevisado, record: item }}
        />
      </Fragment>
    )
  }

  renderItemHeader() {
    const item = Object.assign(
      { label: this.props.item.id ? HallazgoUtils.formatName(this.props.item.name) : 'Hallazgo Nuevo' },
      this.props.item
    )

    return <MenuItemNewHeader item={item} />
  }

  render() {
    const { item } = this.props
    const { value } = this.state

    return (
      <Fragment>
        <div className='content'>
          {this.renderItemHeader()}

          {item.id && (
            <div className='description-text'>
              <strong>NOTA:</strong> El hallazgo debe describir la situacin actual, causa, impacto y recomendacin.
            </div>
          )}
          <EditorPreview
            className={'editor-preview-hallazgo'}
            wrapperClassName={cx(!item.id || 'ui segment')}
            data={value.hallazgo}
            inline={item.id == null}
            ckeditorOptions={{
              attach_disableText:
                'Esta opcin (adjuntar archivos) slo estar disponible una vez haya guardado este hallazgo.',
              height: item.id ? 300 : 150
            }}
            ckeditorUploadOptions={{ model: `${this.props.moduleParams.model}__hallazgo`, modelId: item.id }}
            footer='AttachmentList'
            onSave={this.onEditorChange('hallazgo')}
            previewStyle={{ overflowY: 'auto', maxHeight: 200, width: '100%' }}
            label='<div class="description-text"><strong>NOTA:</strong> El hallazgo debe describir la situacin actual, causa, impacto y recomendacin</div>'
          />

          <hr />

          <div className='ui segment'>
            <EditorPreview
              data={value.plan_accion}
              prefixPreview='Descripcin Plan de Accin: '
              labelTitle='Descripcin Plan de Accin:'
              ckeditorOptions={{
                attach_disableText:
                  'Esta opcin (adjuntar archivos) slo estar disponible una vez haya guardado este hallazgo.'
              }}
              ckeditorUploadOptions={{ model: `${this.props.moduleParams.model}__plan_accion`, modelId: item.id }}
              footer='AttachmentList'
              onSave={this.onEditorChange('plan_accion')}
            />
          </div>

          <div className='ui three column grid'>
            <div className='column'>
              <label htmlFor='hallazgo_modal__responsable'>Responsable del Plan Accin: </label>
              <HallazgoResponsableInput
                value={value.responsable}
                onChange={this.onValueChange('responsable')}
                attrs={{ placeholder: 'Escriba el Nombre', style: { display: 'inline-block' } }}
              />
            </div>

            <div className='column'>
              <label htmlFor='hallazgo_modal__fecha_implementado'>Fecha Implementacin: </label>
              <DatePicker
                id='hallazgo_modal__fecha_implementado'
                dateFormat='yyyy-MM-dd'
                locale='es'
                fixedHeight
                placeholderText='AAAA-mm-dd'
                className='react-datepicker--input'
                selected={this.getDate(value.fecha_implementado)}
                onChange={this.onDateChange('fecha_implementado')}
              />
            </div>

            <div className='column right aligned'>
              <label htmlFor='hallazgo_modal__implementado'>Estado Implementacin: </label>
              <select
                id='hallazgo_modal__implementado'
                value={this.formatImplementadoChoice(value.implementado)}
                onChange={this.onInputChange('implementado')}
              >
                <option value=''>Pendiente</option>
                <option value='0'>En Proceso</option>
                <option value='1'>Hecho</option>
              </select>
            </div>
          </div>
        </div>
        <div className='actions'>
          {this.renderActions()}
          <div className='clearfix' />
        </div>
      </Fragment>
    )
  }
}

type HallazgoResponsableInputProps = {
  value: string
  onChange: (newValue: string) => void
  attrs?: any
}

class HallazgoResponsableInput extends PureComponent<HallazgoResponsableInputProps, { value: string }> {
  users: { id: string; display: string }[]

  mentionStyle = {
    display: 'inline-block',
    minWidth: '200px',
    suggestions: {
      list: {
        backgroundColor: 'white',
        border: '1px solid rgba(0,0,0,0.15)',
        fontSize: 14
      },
      item: {
        padding: '5px 15px',
        borderBottom: '1px solid rgba(0,0,0,0.15)',
        '&focused': {
          backgroundColor: '#cee4e5'
        }
      }
    }
  }

  constructor(props: HallazgoResponsableInputProps) {
    super(props)

    this.state = {
      value: props.value
    }

    this.users = _.map(app.vars.proyecto_equipo, (user: any) => {
      return {
        id: user.user_id,
        display: user.name
      }
    })
  }

  onChange = (event: SyntheticEvent, newValue: string, newPlainTextValue: string, mentions: any) => {
    this.setState({ value: newValue })
    this.props.onChange(newValue)
  }

  render() {
    return (
      <MentionsInput
        singleLine
        type='text'
        value={this.state.value == null ? '' : this.state.value}
        onChange={this.onChange}
        allowSpaceInQuery
        {...this.props.attrs}
        className='mentions-input'
        style={this.mentionStyle}
      >
        <Mention trigger='@' data={this.users} appendSpaceOnAdd />
      </MentionsInput>
    )
  }
}
