export namespace HojaTrabajoUtils {
  export function getAllVisitasAfter(hoja_trabajo: HojaTrabajo, visitaPivot: ProyectoPasoVisita) {
    let visitas: ProyectoPasoVisita[] = []
    let found: boolean = false

    _.forEach(hoja_trabajo.visitas, function(visita) {
      if (found) {
        visitas.push(visita)
        return
      }
      if (visita.id == visitaPivot.id) {
        found = true
      }
    })

    return visitas
  }
}

export namespace HojaTrabajoUpdateUtils {
  /**
   *
   * @param hoja_trabajo Hoja de trabajo a actualizar
   * @param visita Visita a actualizar
   * @param new_data Datos actualizados a aplicar a la visita
   */
  export function updateVisita(
    hoja_trabajo: HojaTrabajo,
    visita: ProyectoPasoVisita | { id: string },
    new_data: Partial<ProyectoPasoVisita>
  ) {
    let hoja_trabajo_copy = Object.assign({}, hoja_trabajo)

    hoja_trabajo_copy.visitas = _.mapValues(hoja_trabajo_copy.visitas, (item) => {
      if (item.id == visita.id) {
        item = Object.assign({}, item, new_data)
      }
      return item
    })

    return hoja_trabajo_copy
  }

  export function updateVisitas(
    hoja_trabajo: HojaTrabajo,
    new_data: { [id_visita: string]: Partial<ProyectoPasoVisita> }
  ) {
    _.forEach(new_data, (visita_new_data, id_visita) => {
      const visita = _.findWhere(hoja_trabajo.visitas, { id_visita })
      hoja_trabajo = updateVisita(hoja_trabajo, visita, visita_new_data)
    })

    return hoja_trabajo
  }

  export function updateField(hoja_trabajo: HojaTrabajo, field: keyof HojaTrabajo, new_data: any) {
    let hoja_trabajo_copy = Object.assign({}, hoja_trabajo)

    hoja_trabajo_copy[field] = Object.assign({}, hoja_trabajo_copy[field], new_data)

    return hoja_trabajo_copy
  }
}
