import { Component } from 'react'
import CKeditor from 'ckeditor4-react'

import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Loader from 'semantic-ui-react/dist/commonjs/elements/Loader'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal'
import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown'

export class HojaTrabajoHistoryModal extends Component<{ trigger: any; url: string }> {
  state = {
    content: ''
  }

  onOpen = () => {
    if (this.state.content == '') {
      const ckeditorOptions = {
        height: 350,
        removePlugins: 'elementspath, uploadfile',
        toolbar: [ {} ]
      }

      this.setState({ content: <Loader active /> })
      $.ajax(this.props.url, { dataType: 'json' }).done((res) => {
        if (res.hasErrors) {
          this.setState({
            content: (
              <div style={{ color: 'red', textAlign: 'center', fontSize: '18px', fontWeight: 'bold', marginTop: 25 }}>
                Hoja de Trabajo NO disponible
              </div>
            )
          })
        } else {
          let content = `&nbsp;<div class='watermark-text' style='color:#08828a33; font-size:70px; font-weight: bold; transform:rotate(340deg); -webkit-transform:rotate(340deg); left: 35%; position: absolute; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none;  '>Hoja Trabajo<br>Ao Anterior</div>
          <div class='watermarked-content' style='position:absolute; z-index:1;  margin-right: 20px; '>${res.response
            .content}</div>&nbsp;`
          this.setState({ content: <CKeditor data={content} readOnly config={ckeditorOptions} /> })
        }
      })
    }
  }

  render() {
    return (
      <Modal
        closeIcon
        trigger={this.props.trigger}
        content={this.state.content}
        header='Hoja de Trabajo - Ao Anterior'
        closeOnDimmerClick={false}
        onOpen={this.onOpen}
        size='fullscreen'
        dimmer='inverted'
        style={{ minHeight: 400 }}
        className='modal-ab'
      />
    )
  }
}

interface HojaTrabajoAsignarVisitaModalProps {
  visita: ProyectoPasoVisita
  onSave: (id_usuario_asignado: string) => void
  onClose: () => void
}

export class HojaTrabajoAsignarVisitaModal extends Component<
  HojaTrabajoAsignarVisitaModalProps,
  { id_asignado: string }
> {
  constructor(props: HojaTrabajoAsignarVisitaModalProps) {
    super(props)

    this.state = {
      id_asignado: props.visita.id_usuario_asignado
    }
  }

  onChange = (ev: React.SyntheticEvent, data: any) => {
    this.setState({ id_asignado: data.value })
  }

  onSave = () => {
    this.props.onSave(this.state.id_asignado)
    this.props.onClose()
  }

  onClose = () => {
    this.props.onClose()
  }

  render() {
    const { visita } = this.props

    let equipo = _.filter(visita.HojaTrabajo.equipo, function(item) {
      return item.rol.is_assignable
    })

    const equipoOptions: any = _.map(equipo, function(item) {
      return {
        key: item.user_id,
        text: item.name,
        value: item.user_id,
        description: item.rol.name,
        label: { content: item.sigla, circular: true, color: 'green' }
      }
    })

    return (
      <Modal
        closeIcon
        open
        closeOnDimmerClick={false}
        size='tiny'
        dimmer='inverted'
        className='modal-ab'
        onClose={this.onClose}
      >
        <Modal.Header>Asignar/Reasignar visita a usuario</Modal.Header>
        <Modal.Content>
          <blockquote>
            <strong>Visita: </strong> {visita.label}
          </blockquote>
          <Dropdown
            placeholder='Seleccione el usuario'
            defaultValue={visita.id_usuario_asignado}
            selection
            clearable
            fluid
            options={equipoOptions}
            onChange={this.onChange}
          />
        </Modal.Content>
        <Modal.Actions>
          <Button primary content='Aceptar' onClick={this.onSave} />
        </Modal.Actions>
      </Modal>
    )
  }
}
