import React, { PureComponent } from 'react'

import { BaseMenuElement } from './menuitem'
import { MenuItemNew } from './menuitem__new'
import { HallazgoList } from './hallazgo_list'
import { HallazgoUtils } from '../utils_hallazgo'
import { HallazgoModal } from './hallazgo_modal'

type MenuItemHallazgoState = {
  items: ProyectoHallazgo[]
}

export class MenuItemHallazgo extends PureComponent<DefaultMenuElementProps<ProyectoHallazgo>, MenuItemHallazgoState> {
  state: MenuItemHallazgoState = {
    items: null
  }

  addItem(data: any) {
    this.setState((prevState) => {
      const copy = [ ...prevState.items, data ]

      return { items: copy }
    })
  }

  updateItem(item: ProyectoHallazgo, data: any) {
    this.setState((prevState) => {
      const copy = _.map(prevState.items, (_item) => {
        if (_item.id == item.id) {
          return Object.assign({}, _item, data)
        } else {
          return _item
        }
      })

      return { items: copy }
    })
  }

  deleteItem(item: ProyectoHallazgo) {
    this.setState((prevState) => {
      const copy = _.reject(prevState.items, { id: item.id })

      return { items: copy }
    })
  }

  onMountPanel = () => {
    if (this.state.items === null) {
      this.props.modelManager.getJson(this.props).then((items) => {
        this.setState({ items })
      })
    }
  }

  onCreateItem = (data: ProyectoHallazgo) => {
    return new Promise<null>((resolve) => {
      $.blockUI(create_blockUI_loading_message('Creando Hallazgo'))

      this.props.modelManager.createItem(this.props, data).then((item) => {
        this.addItem(item)
        $.unblockUI()
        resolve()
      })
    })
  }

  onSaveItem = (item: ProyectoHallazgo, changes: any, blockingText?: string) => {
    return new Promise<ProyectoHallazgo>((resolve) => {
      if (blockingText) {
        $.blockUI(create_blockUI_loading_message(blockingText))
      } else {
        this.updateItem(item, changes)
      }

      this.props.modelManager.saveItem(this.props, item, changes).then((updatedItem) => {
        this.updateItem(item, updatedItem)
        if (blockingText) {
          $.unblockUI()
        }

        resolve(updatedItem)
      })
    })
  }

  onClickDeleteItem = (item: ProyectoHallazgo) => {
    bootbox.warningConfirm(
      `Desea eliminar el hallazgo <strong>${HallazgoUtils.formatName(item.name)}</strong> ?`,
      'No',
      'S, Eliminar',
      (answer: boolean) => {
        if (!answer) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Hallazgo'))

        this.props.modelManager.deleteItem(this.props, item).then(() => {
          this.deleteItem(item)
          $.unblockUI()
        })
      }
    )
  }

  render() {
    return (
      <BaseMenuElement
        name='hallazgo'
        counter={this.state.items === null ? this.props.counter : _.size(this.state.items)}
        icon='hallazgos'
        title='Hallazgos'
        open={this.props.open}
        onToggle={this.props.onOpen}
        onMountPanel={this.onMountPanel}
      >
        <div className='content'>
          <MenuItemNew
            moduleParams={this.props.moduleParams}
            buttonText='Crear Hallazgo'
            headerText='Nuevo Hallazgo'
            isEditable={es_proyecto_editable() && user_has_role_permission('hallazgo_create')}
            item={createNewHallazgo()}
            onSave={this.onCreateItem}
            contentAs={HallazgoModal}
          />
          <HallazgoList
            isEditable={es_proyecto_editable() && user_has_role_permission('hallazgo_edit')}
            moduleParams={this.props.moduleParams}
            items={this.state.items}
            onSaveItem={this.onSaveItem}
            onClickDeleteItem={this.onClickDeleteItem}
          />
        </div>
      </BaseMenuElement>
    )
  }
}

function createNewHallazgo(): ProyectoHallazgo {
  return {
    id: null,
    status: 'NUEVO',
    implementado: null,
    hallazgo: '',
    RevisionLog: [],
    UpdateLog: []
  }
}
